import { Box, Button, FormControl, Select, Typography } from '@material-ui/core';
import { itIT } from '@material-ui/core/locale';
import { ThemeProvider } from '@material-ui/core/styles';
import createMuiTheme from '@material-ui/core/styles/createMuiTheme';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { Pagination } from '@material-ui/lab';
import { format, parseISO } from 'date-fns';
import MaterialTable, { MTableBodyRow } from 'material-table';
import React, { useMemo, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import actionManager from '../../context/action/ActionManager';
import TaskManager from '../../context/task/TaskManager';
import { OPTIONS_STATE } from '../__types__/types';
import InputSelect from '../Input/InputSelect';
import { actionInfoTableStyle, paginationStyle } from './style/actionInfoTableStyle';
import tableIcons from './tableIcons';

const theme = createMuiTheme(
  {
    palette: {
      secondary: {
        main: '#b39f69',
      },
    },
  },
  itIT
);

const options = {
  draggable: false,
  showTitle: false,
  search: false,
  selection: false,
  emptyRowsWhenPaging: false,
  rowStyle: rowData => ({
    backgroundColor: rowData.tableData.checked ? '#b39f6933' : '',
  }),
  headerStyle: {
    color: '#9498a2',
  },
  sorting: true,
  paging: false,
  pageSize: 5,
  pageSizeOptions: [5, 10, 20],
  toolbar: false,
};

const ActionInfoTable = () => {
  const tableRef = useRef(null);
  const { t } = useTranslation();
  const { actions, setSelectAction, pagination, loading, updateActionStatus } = actionManager();
  const { metadata } = TaskManager();
  const classes = actionInfoTableStyle();

  const history = useHistory();
  const { number, totalElements } = pagination;

  const handleChangeStatus = (status, id) => {
    updateActionStatus(id, status);
  };

  const columns = useMemo(() => [
    {
      title: 'ID',
      field: 'id',
      width: '20%',
      align: 'center',
      defaultSort: 'asc',
      type: 'numeric',
      render: row => (
        <Typography variant="subtitle2" component="div">
          {row.id}
        </Typography>
      ),
    },
    {
      title: 'AZIONE',
      field: 'title',
      filtering: true,
      align: 'justify',
      render: row => (
        <Typography variant="body2" component="div">
          {row.title}
        </Typography>
      ),
    },
    {
      title: 'STAT0',
      field: 'status',
      filtering: true,
      align: 'center',
      render: row => (
        <InputSelect
          name="status"
          options={OPTIONS_STATE}
          initialValue={row.status ?? ''}
          emptyOption={t('entities.action.noStatus')}
          highlightKey={OPTIONS_STATE[0].id}
          disabledKey={row.canComplete === false ? OPTIONS_STATE[1].id : null}
          onChange={value => {
            if (row.status !== value) {
              handleChangeStatus(value, row.id);
            }
          }}
          disabled={!metadata?.canChangeCompletionStatus}
        />
      ),
    },
    {
      title: 'FINE',
      field: 'endDate',
      filtering: true,
      type: 'date',
      align: 'center',
      render: row => (
        <Typography variant="body2" component="div">
          {row.endDate ? format(parseISO(row.endDate), 'dd/MM/yyyy') : '-'}
        </Typography>
      ),
    },
    {
      title: 'AZIONE',
      align: 'center',
      field: null,
      render: row => (
        <Button
          data-testid={`action-detail-${row.id}`}
          variant="contained"
          size="large"
          className={classes.buttonDetail}
          onClick={() => {
            setSelectAction(row.index);
            history.push('/dettaglio-azione');
          }}
          endIcon={<ArrowForwardIosIcon />}
        >
          {t('entities.action.gotoDetail')}
        </Button>
      ),
    },
  ]);

  const localization = {
    toolbar: {
      nRowsSelected: `{0} ${t('entities.table.rowSelected')}`,
    },
    body: {
      emptyDataSourceMessage: t('entities.table.empty'),
    },
    pagination: {
      labelRowsSelect: t('entities.table.pagination.rows'),
      labelRowsPerPage: t('entities.table.pagination.rowsPerPage'),
      firstAriaLabel: '',
      firstTooltip: '',
      previousAriaLabel: '',
      previousTooltip: '',
      nextAriaLabel: '',
      nextTooltip: '',
      lastAriaLabel: '',
      lastTooltip: '',
    },
  };

  return (
    <ThemeProvider theme={theme}>
      <MaterialTable
        tableRef={tableRef}
        style={{ position: 'relative' }}
        isLoading={loading}
        icons={tableIcons}
        columns={columns}
        data={actions}
        options={options}
        title=""
        localization={localization}
        components={{
          Pagination: props => (
            <CustomPagination
              // eslint-disable-next-line react/jsx-props-no-spreading
              {...props}
              count={totalElements}
              page={number}
            />
          ),
          // eslint-disable-next-line react/jsx-props-no-spreading
          Row: props => <MTableBodyRow {...props} className="task-list-row" />,
        }}
      />
    </ThemeProvider>
  );
};

const CustomPagination = props => {
  const {
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    count,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    rowsPerPage,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    rowsPerPageOptions,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    onChangeRowsPerPage,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    onChangePage,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    page,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
  } = props;

  // eslint-disable-next-line react/prop-types
  const pageNumber = Math.ceil(count / rowsPerPage);

  const handleChangeRowsPerPage = event => {
    const evt = { target: { value: parseInt(event.target.value, 10) } };
    onChangeRowsPerPage(evt);
  };

  const handleChangePage = (event, newPage) => {
    onChangePage(event, newPage - 1);
  };

  const hanldeNextAndPreviousPage = (event, newPage) => {
    onChangePage(event, newPage < 0 ? 0 : newPage);
  };

  const rowsPerPageOptionsMemo = React.useMemo(() =>
    // eslint-disable-next-line react/prop-types
    rowsPerPageOptions.map(m => (
      <option key={m} value={m}>
        {m}
      </option>
    ))
  );

  const { t } = useTranslation();
  const css = paginationStyle();
  return (
    <td className={css.td}>
      <Box
        textAlign="left"
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="flex-start"
        alignItems="center"
        alignContent="flex-start"
        position="absolute"
        top={0}
        right="5%"
      >
        <Box marginRight="20px">
          <Typography variant="body1" color="textSecondary">
            {t('entities.table.pagination.rowsPerPage')}
          </Typography>
        </Box>
        <Box className={css.rowsPerPage}>
          <FormControl variant="outlined" className={css.formControl} fullWidth>
            <Select native value={rowsPerPage} onChange={handleChangeRowsPerPage}>
              {rowsPerPageOptionsMemo}
            </Select>
          </FormControl>
        </Box>
      </Box>
      <Box
        textAlign="left"
        p={2}
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="space-between"
        alignItems="center"
        alignContent="flex-start"
      >
        <Button
          onClick={evt => hanldeNextAndPreviousPage(evt, page - 1)}
          disabled={page <= 0}
          size="large"
          variant="outlined"
          className={css.button}
          startIcon={<ArrowBackIosIcon />}
        >
          <Typography variant="body2" color="textSecondary">
            {t('entities.table.pagination.previous')}
          </Typography>
        </Button>
        <Pagination
          className={css.pagination}
          size="large"
          count={pageNumber}
          page={page + 1}
          variant="outlined"
          shape="rounded"
          hideNextButton
          hidePrevButton
          onChange={handleChangePage}
        />
        <Button
          onClick={evt => hanldeNextAndPreviousPage(evt, page + 1)}
          disabled={parseInt(page + 1 * rowsPerPage, 10) >= parseInt(count, 10)}
          size="large"
          variant="outlined"
          className={css.button}
          endIcon={<ArrowForwardIosIcon />}
        >
          <Typography variant="body2" color="textSecondary">
            {t('entities.table.pagination.next')}
          </Typography>
        </Button>
      </Box>
    </td>
  );
};

export default ActionInfoTable;
