import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import {
  apiActionUpdatePut,
  apiSubActionUpdatePut,
  apiTaskAction,
  apiUpdateStatusAction,
  apiUpdateStatusSubAction,
} from '../../api/taskDetail';
import { getUniqueUsers } from '../../helpers/helper';
import TaskManager from '../task/TaskManager';
import UserManager from '../user/UserManager';
import ActionContext from './ActionContext';

const ActionManager = () => {
  const [state, setState] = useContext(ActionContext);

  const { task, fetchTask } = TaskManager();
  const { setUsersPresent } = UserManager();
  const { t } = useTranslation();

  const { serviceUrl } = state;

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => {
    setState(prev => ({ ...prev, message: null }));
  };

  const setSelectAction = index => setState(prev => ({ ...prev, selected: index }));

  const updateAction = async (type, action) => {
    try {
      let response;
      if (type === 'action') {
        response = await apiActionUpdatePut(serviceUrl, action);
      } else {
        response = await apiSubActionUpdatePut(serviceUrl, action);
      }
      if (response.payload.id) {
        let actions = await apiTaskAction(serviceUrl, task.id);
        const { payload } = actions;
        const usersActions = getUniqueUsers(payload);
        setUsersPresent(usersActions);
        actions = payload.map((act, index) => ({ ...act, index }));
        setState(prev => ({ ...prev, actions, message: t('message.status') }));
      }
    } catch (e) {
      console.error('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    }
  };

  const updateStatus = async (type, actionId, status) => {
    try {
      let response;
      if (type === 'action') {
        response = await apiUpdateStatusAction(serviceUrl, actionId, { status });
      } else {
        response = await apiUpdateStatusSubAction(serviceUrl, actionId, { status });
      }
      if (response.payload.id) {
        fetchTask(task.id);
        const actions = await apiTaskAction(serviceUrl, task.id);
        setState(prev => ({
          ...prev,
          actions: actions.payload,
          message: t('message.status'),
        }));
      }
    } catch {
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    }
  };

  const updateActionStatus = async (actionId, status) => {
    updateStatus('action', actionId, status);
  };

  const updateSubActionStatus = async (subActionId, status) => {
    updateStatus('subActions', subActionId, status);
  };

  return {
    taskId: state.taskId,
    usersActions: state.usersActions,
    actions: state.actions,
    subActions: state.actions[state?.selected]?.subActions ?? [],
    error: state.error,
    message: state.message,
    clearError,
    clearMessage,
    setSelectAction,
    actionSelected: state.actions[state.selected],
    selectedAction: state.selectedAction,
    updateActionStatus,
    updateSubActionStatus,
    updateAction,
    pagination: state.pagination,
    loading: state.loading,
  };
};

export default ActionManager;
