import { Box, Grid, LinearProgress, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { differenceInDays } from 'date-fns';
import { progressBar, priorityStyle, progressColor } from './style/statusStyle';
import TaskManager from '../../context/task/TaskManager';
import InputSelect from '../Input/InputSelect';

const Status = ({ priority }) => {
  const { t } = useTranslation();
  const cssPriority = priorityStyle();
  const cssProgressBar = progressBar();
  const { task, updateTaskCompletionPercentage, metadata } = TaskManager();
  const cssColor = progressColor();

  const getPercentageOptions = () => {
    return Array.from(Array(101).keys()).map(element => {
      return { id: element, value: element };
    });
  };

  const updateCompletionPercentage = async value => {
    await updateTaskCompletionPercentage(task.id, value);
  };

  const getProgress = days => {
    let percentage;
    if (days >= 15) {
      percentage = 100;
    } else if (days === 0) {
      return 0;
    } else if (days < 0) {
      return days;
    } else if (days < -999) {
      return -999;
    } else {
      percentage = Math.round((days * 100) / 15);
    }
    return Math.ceil(percentage / 5) * 5;
  };

  const getLinearProgressValue = value => {
    if (value < 0) {
      return 100;
    }
    if (value === 0) {
      return 2;
    }
    return value;
  };

  const progress = getProgress(differenceInDays(new Date(task.endDate), new Date()));

  return (
    <Grid container spacing={1} alignItems="center">
      <Grid item xs={9}>
        <Box alignItems="center">
          <Box minWidth={35}>
            <Box
              display="flex"
              flexDirection="row"
              flexWrap="nowrap"
              justifyContent="space-between"
            >
              <Typography variant="body2" color="textSecondary">
                {t('entities.detail.progress')}
              </Typography>
              <Typography variant="subtitle2" color="textSecondary">
                {`${Math.round(progress || 0)}%`}
              </Typography>
            </Box>
          </Box>
          <Box width="100%" mr={1}>
            <LinearProgress
              classes={cssProgressBar}
              className={`${progress >= 0 ? cssColor[`perc${progress}`] : cssColor.percNegative}`}
              variant="determinate"
              valueBuffer={100}
              value={getLinearProgressValue(progress)}
            />
          </Box>
        </Box>
      </Grid>

      <Grid item xs={3}>
        <Box
          display="flex"
          flexDirection="row"
          alignItems="center"
          flexWrap="nowrap"
          justifyContent="space-between"
          marginLeft={1}
        >
          <Typography variant="body2" color="textSecondary">
            {t('entities.detail.priority')}
          </Typography>
          <div className={`${cssPriority.priority} ${cssPriority[priority]}`}>
            {t(`entities.detail.${priority}`)}
          </div>
        </Box>
      </Grid>
      <Grid item xs={12}>
        <Box className={cssPriority.boxPercent}>
          <Box width="50%" display="flex" alignItems="center" border={0}>
            <Typography variant="body2" color="textSecondary">
              {t('entities.detail.completionPercentage')}
            </Typography>
            <Box border={0} marginLeft={2}>
              {!metadata?.canChangeCompletionStatus ? (
                <Typography variant="h6" color="textSecondary">
                  {task.completionPercentage ?? 0}
                </Typography>
              ) : (
                <InputSelect
                  dataTestId="select-completionPercentage"
                  name="completionPercentage"
                  options={getPercentageOptions()}
                  initialValue={task.completionPercentage ?? 0}
                  onChange={updateCompletionPercentage}
                  disabled={!metadata?.canChangeCompletionStatus}
                />
              )}
            </Box>
          </Box>
        </Box>
      </Grid>
    </Grid>
  );
};

Status.propTypes = {
  priority: PropTypes.string.isRequired,
};

export default Status;
