import { Box, Button, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import AttachmentManager from '../../context/attachment/AttachmentManager';
import TaskManager from '../../context/task/TaskManager';
import UserManager from '../../context/user/UserManager';
import BoxInfoWithAttachment from '../BoxInfo/BoxInfoWithAttachment';
import Notification from '../common/Notification';
import DialogAttachment from '../Dialog/Attachment/DialogAttachment';
import DialogDHOC from '../Dialog/Attachment/DialogDhOC';
import DialogElCloud from '../Dialog/Attachment/DialogElCloud';
import attachmentInfoStyle from './style/attachmentInfoStyle';

const AttachmentInfo = ({ referenceId, referenceType, domain }) => {
  const css = attachmentInfoStyle();
  const { t } = useTranslation();
  const [openDialog, setOpenDialog] = useState(false);
  const [openDialogDhOC, setOpenDialogDhOC] = useState(false);
  const [openDialogElCloud, setOpenDialogElCloud] = useState(false);
  const {
    fetchAttachments,
    attachments,
    EINET_DIRECTORY,
    allClouds,
    error,
    message,
    clearError,
    clearMessage,
  } = AttachmentManager();
  const { users } = UserManager();
  const { edoResponsible } = TaskManager();

  useEffect(() => {
    if (referenceId && referenceType) {
      fetchAttachments(referenceId, referenceType);
    }
  }, [referenceId, referenceType]);

  const einetAttachments = attachments
    .filter(a => a.file.cloudType === allClouds.NEXTCLOUD)
    ?.sort((a, b) => (a.id > b.id ? -1 : 1));
  const adhocAttachments = attachments
    .filter(a => a.file.cloudType === allClouds.ADHOC)
    ?.sort((a, b) => (a.id > b.id ? -1 : 1));

  const handleDialog = value => {
    setOpenDialog(!openDialog);
    if (value === 'dhoc') {
      setOpenDialogDhOC(true);
    } else if (value === 'elcloud') {
      setOpenDialogElCloud(true);
    }
  };

  let linkOffice;
  let linkDepartment;
  if (users) {
    // type 3 Office
    linkOffice =
      users[edoResponsible]?.edo?.type.id === 3
        ? users[edoResponsible].edo?.edoInfos[0]?.value
        : null;

    // type 2 Reparto
    linkDepartment =
      users[edoResponsible]?.edo?.parentEdo?.type.id === 2
        ? users[edoResponsible]?.edo?.parentEdo?.edoInfos[0]?.value
        : null;
  }

  return (
    <>
      {message === null && error === null ? null : (
        <Notification
          message={message || error}
          onClose={() => (message ? clearMessage() : clearError())}
          status={message ? 'success' : 'error'}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        />
      )}
      <Box className={css.boxContainer}>
        <Box textAlign="center">
          <Button
            size="large"
            variant="contained"
            disableRipple
            className={css.button}
            startIcon={<AddIcon />}
            onClick={() => setOpenDialog(true)}
          >
            <Typography variant="body2" color="textSecondary">
              {domain === 'ACTION'
                ? t('entities.attachment.addAttachmentAction')
                : t('entities.attachment.addAttachment')}
            </Typography>
          </Button>
        </Box>
        <Box className={css.boxButtons}>
          {linkDepartment ? (
            <Button
              disableRipple
              className={css.buttonLink}
              href={`${EINET_DIRECTORY}${linkDepartment}`}
              target="_blank"
            >
              <Typography variant="body2">{t('entities.attachment.folderDepartment')}</Typography>
            </Button>
          ) : (
            <Typography variant="body2" color="error">
              {t('entities.attachment.attachmentDirNotPresent', {
                dir: t('entities.attachment.folderDepartment'),
              })}
            </Typography>
          )}

          {/* Rimosso per issue https://entando.myjetbrains.com/youtrack/issue/SMETT-40
            {linkOffice ? (
            <Button
              disableRipple
              className={css.buttonLink}
              href={`${EINET_DIRECTORY}${linkOffice}`}
              target="_blank"
            >
              <Typography variant="body2">{t('entities.attachment.folderOffice')}</Typography>
            </Button>
          ) : (
            <Typography variant="body2" color="error">
              {t('entities.attachment.attachmentDirNotPresent', { dir: 'Ufficio' })}
            </Typography>
          )} */}
        </Box>
        <Box textAlign="left" marginTop={2}>
          <Box p={1}>
            <Typography variant="body2" color="textSecondary">
              {t('entities.attachment.einetAttachments', { count: einetAttachments.length })}
            </Typography>
          </Box>
          {einetAttachments.map(a => (
            <Box key={a.id}>
              <BoxInfoWithAttachment
                id={a.id}
                filename={a.file.name}
                signature={a.file.signature}
                cloudType={a.file.cloudType}
                allClouds={allClouds}
                referenceId={referenceId}
                referenceType={referenceType}
              />
            </Box>
          ))}
        </Box>
        <Box textAlign="left" marginTop={2}>
          <Box p={1}>
            <Typography variant="body2" color="textSecondary">
              {t('entities.attachment.adhocAttachments', { count: adhocAttachments.length })}
            </Typography>
          </Box>
          {adhocAttachments.map(a => (
            <Box key={a.id}>
              <BoxInfoWithAttachment
                id={a.id}
                filename={a.file.name}
                signature={a.file.signature}
                cloudType={a.file.cloudType}
                allClouds={allClouds}
                referenceId={referenceId}
                referenceType={referenceType}
              />
            </Box>
          ))}
        </Box>
        <DialogAttachment onClose={handleDialog} open={openDialog} />
        <DialogDHOC
          onClose={() => setOpenDialogDhOC(false)}
          open={openDialogDhOC}
          referenceId={referenceId}
          referenceType={referenceType}
        />
        <DialogElCloud
          onClose={() => setOpenDialogElCloud(false)}
          open={openDialogElCloud}
          referenceId={referenceId}
          referenceType={referenceType}
        />
      </Box>
    </>
  );
};

AttachmentInfo.propTypes = {
  referenceType: PropTypes.string,
  referenceId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  domain: PropTypes.string,
};
AttachmentInfo.defaultProps = {
  referenceType: null,
  referenceId: null,
  domain: PropTypes.string,
};

export default AttachmentInfo;
