import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import {
  apiActionsNotesGet,
  apiActionsNotesPost,
  apiNotesDelete,
  apiNotesGet,
  apiNotesPost,
} from '../../api/taskDetail';

import loadingManager from '../loading/LoadingManager';
import NoteContext from './NoteContext';

const NoteManager = () => {
  const [state, setState] = useContext(NoteContext);
  const { setLoading } = loadingManager();
  const { t } = useTranslation();
  const { serviceUrl } = state;

  const fetchNotes = async (referenceId, referenceType) => {
    try {
      let json;
      if (referenceType === 'TASK') {
        json = await apiNotesGet(serviceUrl, referenceId);
      } else {
        json = await apiActionsNotesGet(serviceUrl, referenceId);
      }
      const notes = json.payload;
      setState(prevState => ({ ...prevState, notes }));
    } catch (e) {
      setState({ ...state, error: t('error.dataLoading'), message: null });
    }
  };

  const postNote = async (referenceId, referenceType, note) => {
    try {
      setLoading(true);
      let response;
      if (referenceType === 'TASK') {
        response = await apiNotesPost(serviceUrl, referenceId, note.description);
      } else {
        response = await apiActionsNotesPost(serviceUrl, referenceId, note.description);
      }
      if (response.payload.id) {
        let json;
        if (referenceType === 'TASK') {
          json = await apiNotesGet(serviceUrl, referenceId);
        } else {
          json = await apiActionsNotesGet(serviceUrl, referenceId);
        }
        const notes = json.payload;
        setState(prev => ({
          ...prev,
          notes,
          message: t('message.noteCreated', { id: response.payload.id }),
        }));
      }
      return response.payload.id;
    } catch (e) {
      console.log('errore', e);
      setState({ ...state, error: t('error.dataLoading'), message: null });
    } finally {
      setLoading(false);
    }
    return null;
  };

  const deleteNote = async (referenceId, referenceType, noteId) => {
    try {
      setLoading(true);
      await apiNotesDelete(serviceUrl, noteId);
      let json;
      if (referenceType === 'TASK') {
        json = await apiNotesGet(serviceUrl, state.taskId);
      } else {
        json = await apiActionsNotesGet(serviceUrl, referenceId);
      }
      const notes = json.payload;
      setState(prev => ({
        ...prev,
        notes,
        message: t('message.noteDeleted', { id: noteId }),
      }));
      return null;
    } catch (e) {
      console.log('errore', e);
      setState({ ...state, error: t('error.dataLoading'), message: null });
    } finally {
      setLoading(false);
    }
    return null;
  };

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  return {
    notes: state.notes,
    fetchNotes,
    postNote,
    deleteNote,
    error: state.error,
    message: state.message,
    clearError,
    clearMessage,
  };
};
export default NoteManager;
