import { Box, IconButton, Typography } from '@material-ui/core';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import PropTypes from 'prop-types';
import React from 'react';
import LinkIcon from '@material-ui/icons/Link';
import DeleteIcon from '@material-ui/icons/Delete';
import AttachmentManager from '../../context/attachment/AttachmentManager';
import TaskManager from '../../context/task/TaskManager';
import boxInfoWithAttachment from './style/boxInfoWithAttachmentStyle';

const BoxInfoWithAttachment = ({
  id,
  filename,
  signature,
  cloudType,
  allClouds,
  referenceType,
  referenceId,
}) => {
  const { deleteAttachment } = AttachmentManager();
  const { metadata } = TaskManager();
  const { downloadAdhocAttachment } = AttachmentManager();

  const css = boxInfoWithAttachment();

  const link = filename;
  let label = filename;
  if (label.length > 60) {
    label = `${filename.substring(0, 17)}...${filename.substring(filename.length - 40)}`;
  }

  return (
    <Box
      className={`${css.borderBox} ${css.marginBox}`}
      textAlign="left"
      p={2}
      display="flex"
      flexDirection="row"
      flexWrap="nowrap"
      justifyContent="space-between"
      alignItems="center"
      alignContent="flex-start"
    >
      <Box p={1} className={css.icon}>
        <AttachFileIcon />
      </Box>
      <Box className={css.attachment}>
        {cloudType === allClouds.NEXTCLOUD ? (
          <Typography variant="body1" color="textSecondary" title={link}>
            <a target="_blank" rel="noopener noreferrer" href={link} className={css.link}>
              {label}
            </a>
          </Typography>
        ) : (
          <>
            {signature ? (
              <Typography
                title={label}
                component="div"
                variant="subtitle2"
                noWrap
                style={{ cursor: 'pointer' }}
              >
                {signature}
              </Typography>
            ) : null}
            <Typography
              variant="body1"
              color="textSecondary"
              title={label}
              component="div"
              noWrap
              style={{ cursor: 'pointer' }}
              onClick={() => downloadAdhocAttachment(id, null, true)}
            >
              {label}
            </Typography>
          </>
        )}
      </Box>
      <Box className={`${css.icon} ${css.iconAction}`}>
        {cloudType === allClouds.NEXTCLOUD ? (
          <a target="_blank" rel="noopener noreferrer" href={link} className={css.link}>
            <LinkIcon />
          </a>
        ) : (
          <LinkIcon
            style={{ cursor: 'pointer' }}
            onClick={() => downloadAdhocAttachment(id, null, true)}
          />
        )}
        {metadata.canRemoveAttachments ? (
          <IconButton
            aria-label="delete"
            onClick={() => deleteAttachment(id, referenceType, referenceId)}
            disableRipple
          >
            <DeleteIcon />
          </IconButton>
        ) : null}
      </Box>
    </Box>
  );
};

BoxInfoWithAttachment.propTypes = {
  id: PropTypes.string.isRequired,
  filename: PropTypes.string.isRequired,
  signature: PropTypes.string,
  cloudType: PropTypes.string.isRequired,
  allClouds: PropTypes.shape(null).isRequired,
  referenceType: PropTypes.string,
  referenceId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
};

BoxInfoWithAttachment.defaultProps = {
  referenceType: null,
  signature: null,
};

export default BoxInfoWithAttachment;
