import { Box, Grid, Typography } from '@material-ui/core';
import { itIT } from '@material-ui/core/locale';
import { ThemeProvider } from '@material-ui/core/styles';
import createMuiTheme from '@material-ui/core/styles/createMuiTheme';
import { format, parseISO } from 'date-fns';
import { get } from 'lodash';
import MaterialTable from 'material-table';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import ActionManager from '../../context/action/ActionManager';
import DialogManager from '../../context/dialog/DialogManager';
import userManager from '../../context/user/UserManager';
import { ROLES } from '../../helpers/helper';
import { OPTIONS_STATE } from '../__types__/types';
import tableIcons from '../Action/tableIcons';
import BoxInfo from '../BoxInfo/BoxInfo';
import BoxInfoWithAvatar from '../BoxInfo/BoxInfoWithAvatar';
import DialogUsers from '../Dialog/Users/DialogUsers';
import InputSelect from '../Input/InputSelect';

const theme = createMuiTheme(
  {
    palette: {
      secondary: {
        main: '#b39f69',
      },
    },
  },
  itIT
);

const options = {
  draggable: false,
  showTitle: false,
  search: false,
  selection: false,
  emptyRowsWhenPaging: false,
  rowStyle: rowData => ({
    backgroundColor: rowData.tableData.checked ? '#b39f6933' : '',
  }),
  headerStyle: {
    color: '#9498a2',
  },
  sorting: true,
  paging: false,
  pageSize: 5,
  pageSizeOptions: [5, 10, 20],
  toolbar: false,
};

const DetailActionInfoTable = () => {
  const tableRef = useRef(null);
  const { subActions, loading, updateSubActionStatus, updateAction } = ActionManager();
  const { users } = userManager();
  const { t } = useTranslation();
  const dialogManager = DialogManager();
  const [usersSelected, setUsersSelected] = useState({});
  const [subActionSelected, setSubActionSelected] = useState({});

  const localization = {
    toolbar: {
      nRowsSelected: `{0} ${t('entities.table.rowSelected')}`,
    },
    body: {
      emptyDataSourceMessage: t('entities.table.empty'),
    },
    pagination: {
      labelRowsSelect: t('entities.table.pagination.rows'),
      labelRowsPerPage: t('entities.table.pagination.rowsPerPage'),
      firstAriaLabel: '',
      firstTooltip: '',
      previousAriaLabel: '',
      previousTooltip: '',
      nextAriaLabel: '',
      nextTooltip: '',
      lastAriaLabel: '',
      lastTooltip: '',
    },
  };

  const handleChangeStatus = (status, id) => {
    updateSubActionStatus(id, status);
  };

  const columns = useMemo(() => [
    {
      title: 'ID',
      width: '20%',
      align: 'center',
      defaultSort: 'asc',
      type: 'numeric',
      render: row => (
        <Typography variant="subtitle2" component="div">
          {row.id}
        </Typography>
      ),
    },
    {
      title: 'SOTTOAZIONE',
      align: 'justify',
      render: row => (
        <Typography variant="body2" component="div">
          {row.title}
        </Typography>
      ),
    },
    {
      title: 'STATO',
      filtering: true,
      align: 'center',
      render: row => (
        <InputSelect
          name="status"
          options={OPTIONS_STATE}
          initialValue={row.status || ''}
          emptyOption={t('entities.action.noStatus')}
          highlightKey={OPTIONS_STATE[0].id}
          disabledKey={row.canComplete === false ? OPTIONS_STATE[1].id : null}
          onChange={value => {
            if (row.status !== value) {
              handleChangeStatus(value, row.id);
            }
          }}
          // disabled={!metadata?.canChangeCompletionStatus}
        />
      ),
    },
    {
      title: 'FINE',
      filtering: true,
      type: 'date',
      align: 'center',
      render: row => (
        <Typography variant="body2" component="div">
          {row.endDate ? format(parseISO(row.endDate), 'dd/MM/yyyy') : '-'}
        </Typography>
      ),
    },
  ]);

  const openDialog = (role, user, subAction) => {
    setSubActionSelected(subAction);
    dialogManager.setAllState({
      type: 'subActions',
      openDialog: true,
      userSelected: user,
      userRole: role,
    });
  };

  const closeDialog = () => {
    dialogManager.setOpenDialog(false);
  };

  const showBoxWithAvatar = id => {
    const info = subActions.find(f => f.id === id);
    return Array.from(['responsible', 'accountable', 'consulted', 'informed']).map(m => (
      <Grid item xs={12} key={m}>
        <BoxInfoWithAvatar
          title={t(`entities.info.${m}`)}
          name={users[info[m]] ? `${users[info[m]]?.firstname} ${users[info[m]]?.lastname}` : ''}
          role={get(users[`${info[m]}`], 'role', t('entities.info.empty'))}
          avatar={`${info[m]}.jpg`}
          cta={() => openDialog(m, users[info[m]], info)}
        />
      </Grid>
    ));
  };

  const handleUserSelected = (user, role) => {
    const newSubAction = { ...subActionSelected, [role]: user.fiscalCode.toUpperCase() };
    delete newSubAction.tableData;
    delete newSubAction.index;
    updateAction('subActions', newSubAction);
  };

  useEffect(() => {
    setUsersSelected(
      Array.from(ROLES).reduce((acc, role) => {
        if (users[subActionSelected[role]]) {
          acc[role] = users[subActionSelected[role]].fiscalCode.toUpperCase();
        }
        return acc;
      }, {})
    );
  }, [subActionSelected]);

  return (
    <>
      <DialogUsers
        open={dialogManager.isOpenDialog}
        handleClose={closeDialog}
        handleUserSelected={handleUserSelected}
        usersSelected={usersSelected}
        role={dialogManager.userRole}
      />
      <ThemeProvider theme={theme}>
        <MaterialTable
          tableRef={tableRef}
          style={{ position: 'relative' }}
          isLoading={loading}
          icons={tableIcons}
          columns={columns}
          data={subActions}
          options={options}
          title=""
          localization={localization}
          detailPanel={rowData => {
            return (
              <Box p={1}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <BoxInfo
                      title={t('entities.action.detail.description')}
                      content={rowData.description}
                    />
                  </Grid>
                  {showBoxWithAvatar(rowData.id)}
                </Grid>
              </Box>
            );
          }}
        />
      </ThemeProvider>
    </>
  );
};

export default DetailActionInfoTable;
