import React, { useEffect } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import LoadingManager from '../../context/loading/LoadingManager';
import TaskManager from '../../context/task/TaskManager';
import UserManager from '../../context/user/UserManager';
import ViewProvider from '../../context/view/ViewProvider';
import Spinner from '../Spinner/Spinner';
import ActionDetailPage from './ActionDetailPage';
import TaskDetailPage from './TaskDetailPage';

const AppPage = () => {
  const { fetchUserPresent, usersPresent } = UserManager();
  const { edoResponsible } = TaskManager();
  const { isLoading } = LoadingManager();

  useEffect(() => {
    if (usersPresent || edoResponsible) {
      fetchUserPresent(edoResponsible);
    }
  }, [usersPresent, edoResponsible]);

  return (
    <ViewProvider>
      {isLoading ? <Spinner /> : null}
      <HashRouter hashType="noslash">
        <Switch>
          <Route exact path="/dettaglio-azione*">
            <ActionDetailPage />
          </Route>
          <Route>
            <TaskDetailPage />
          </Route>
        </Switch>
      </HashRouter>
    </ViewProvider>
  );
};

export default AppPage;
