import { FormControl, Select } from '@material-ui/core';

import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import selectStyle from './style/inputSelectStyle';

const InputSelect = ({
  name,
  options,
  emptyOption,
  initialValue,
  highlightKey,
  disabledKey,
  onChange,
  disabled,
  register,
  // eslint-disable-next-line react/prop-types
  dataTestId,
}) => {
  const [value, setValue] = useState(initialValue ?? '');

  useEffect(() => {
    if (initialValue) {
      setValue(initialValue);
    }
  }, [initialValue]);

  const handleChange = event => {
    if (event.target.value) {
      if (String(event.target.value) !== String(value)) {
        setValue(event.target.value);
        if (onChange) {
          onChange(event.target.value);
        }
      }
    }
  };
  const css = selectStyle();
  return (
    <FormControl variant="outlined" className={css.formControl} fullWidth>
      <Select
        disabled={disabled}
        native
        inputRef={register}
        value={value}
        // onMouseUp={handleChange}
        onChange={handleChange}
        name={name}
        inputProps={{
          className: value === highlightKey ? css.highlight : null,
          'data-testid': dataTestId,
        }}
      >
        {emptyOption ? (
          <option value="" disabled>
            {emptyOption}
          </option>
        ) : null}
        {options.map(m => (
          <option
            key={m.id}
            value={m.id}
            disabled={disabledKey === m.id}
            className={m.id === highlightKey ? css.highlight : null}
          >
            {m.value}
          </option>
        ))}
      </Select>
    </FormControl>
  );
};

InputSelect.propTypes = {
  name: PropTypes.string.isRequired,
  emptyOption: PropTypes.string,
  options: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  initialValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  highlightKey: PropTypes.string,
  disabledKey: PropTypes.string,
  onChange: PropTypes.func,
  disabled: PropTypes.bool,
  register: PropTypes.func,
};

InputSelect.defaultProps = {
  initialValue: '',
  emptyOption: undefined,
  highlightKey: '',
  disabledKey: undefined,
  onChange: undefined,
  disabled: false,
  register: () => {},
};

export default InputSelect;
