import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { apiAttachmentGet, apiAttachmentGetAllReferenceAndCloudType } from '../../api/taskDetail';
import loadingManager from '../loading/LoadingManager';
import AttachmentContext from './AttachmentContext';

const AttachmentProvider = ({ children, keycloak, serviceUrlAttachments, taskId }) => {
  const [state, setState] = useState({
    attachments: [],
    taskId,
    error: null,
    message: null,
    keycloak,
    serviceUrlAttachments,
    allClouds: {},
    allReferenceType: {
      TASK: null,
      ACTION: null,
    },
    EINET_DIRECTORY: 'https://eicloud.comc4ei.esercito.difesa.it/index.php/apps/files/?dir=',
  });
  const { setLoading } = loadingManager();

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        if (taskId !== null) {
          const jsonTypes = await apiAttachmentGetAllReferenceAndCloudType(serviceUrlAttachments);
          const allClouds = jsonTypes.payload.clouds;
          const allReferenceType = jsonTypes.payload.references;
          const jsonAttachments = await apiAttachmentGet(
            serviceUrlAttachments,
            taskId,
            allReferenceType.TASK
          );
          const { attachments } = jsonAttachments.payload;
          setState(prevState => ({
            ...prevState,
            taskId,
            attachments,
            allClouds,
            allReferenceType,
          }));
        }
      } catch (err) {
        console.error('errore', err);
        setState(prevState => ({ ...prevState, attachments: [], error: err.toString() }));
      } finally {
        setLoading(false);
      }
    };
    const authenticated = keycloak.initialized && keycloak.authenticated;
    if (authenticated) {
      fetchData();
    }
  }, [keycloak.authenticated, keycloak.initialized, taskId]);

  return (
    <AttachmentContext.Provider value={[state, setState]}>{children} </AttachmentContext.Provider>
  );
};

AttachmentProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  taskId: PropTypes.string.isRequired,
  keycloak: PropTypes.shape({ initialized: PropTypes.bool, authenticated: PropTypes.bool }),
  serviceUrlAttachments: PropTypes.string.isRequired,
};

AttachmentProvider.defaultProps = {
  keycloak: {
    initialized: false,
    authenticated: false,
  },
};
export default AttachmentProvider;
