import { getDefaultOptions, request, requestBlob } from 'api/helpers';

const resource = 'tasks';
export const NEXTCLOUD_API = 'nextcloud';
export const ADHOC_API = 'adhoc';
const SEARCH_ADHOC_API = 'adhoc/search';
const DOWNLOAD_ADHOC_API = 'adhoc/download';
const ALL_TYPES_API = 'all/types';
const referenceTypeRequestParameter = 'referenceType=';

export const apiTaskDetail = async (serviceUrl, taskId) => {
  const url = `${serviceUrl}/${resource}/${taskId}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiTaskUpdatePut = async (serviceUrl, task) => {
  const url = `${serviceUrl}/${resource}/${task.id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: task ? JSON.stringify(task) : null,
  };
  return request(url, options);
};

