import { Box, Button, Grid, Paper, Typography } from '@material-ui/core';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';

import { isEmpty, omit } from 'lodash';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import ActionManager from '../../context/action/ActionManager';
import AttachmentManager from '../../context/attachment/AttachmentManager';
import DialogManager from '../../context/dialog/DialogManager';
import UserManager from '../../context/user/UserManager';
import { ROLES } from '../../helpers/helper';
import AccordionInfo from '../Action/AccordionInfo';
import Notification from '../common/Notification';
import DetailActionInfo from '../DetailAction/DetailActionInfo';
import DetailActionInfoTable from '../DetailAction/DetailActionInfoTable';
import DialogUsers from '../Dialog/Users/DialogUsers';
import { actionDetailPageStyle, actionDetailPageViewStyle } from './style/actionDetailPageStyle';

const ActionDetailPage = () => {
  const [indexSubAction, setIndexSubAction] = useState(0);
  const [view, setView] = useState('view');
  const {
    actionSelected,
    error,
    message,
    clearError,
    clearMessage,
    updateAction,
  } = ActionManager();

  const { allReferenceType } = AttachmentManager();

  const { t } = useTranslation();
  const history = useHistory();
  const classes = actionDetailPageStyle();
  const cssView = actionDetailPageViewStyle();

  const subActions = (actionSelected && actionSelected.subActions) || [];
  const action = omit(actionSelected, 'subActions');

  const handleStepAction = index => {
    if (index >= 0 && index <= subActions.length) {
      setIndexSubAction(index);
    }
  };

  const handleChange = (event, value) => {
    setView(value);
  };

  const dialogManager = DialogManager();
  const { users } = UserManager();

  const [usersSelected, setUsersSelected] = useState({});

  const closeDialog = () => {
    dialogManager.setOpenDialog(false);
  };

  const handleUserSelected = (user, role) => {
    let newAction;
    if (dialogManager.type === 'action') {
      newAction = { ...action, [role]: user.fiscalCode.toUpperCase() };
    } else {
      newAction = { ...subActions[indexSubAction], [role]: user.fiscalCode.toUpperCase() };
    }
    delete newAction.tableData;
    delete newAction.index;
    updateAction(dialogManager.type, newAction);
  };

  useEffect(() => {
    if (dialogManager.type === 'action') {
      setUsersSelected(
        Array.from(ROLES).reduce((acc, role) => {
          if (users[action[role]]) {
            acc[role] = users[action[role]].fiscalCode.toUpperCase();
          }
          return acc;
        }, {})
      );
    } else if (dialogManager.type === 'subAction') {
      const subAction = subActions[indexSubAction];
      setUsersSelected(
        Array.from(ROLES).reduce((acc, role) => {
          if (users[subAction[role]]) {
            acc[role] = users[subAction[role]].fiscalCode.toUpperCase();
          }
          return acc;
        }, {})
      );
    }
  }, [dialogManager.type]);

  return !isEmpty(actionSelected) ? (
    <Paper elevation={5}>
      {message === null && error === null ? null : (
        <Notification
          message={message || error}
          onClose={() => (message ? clearMessage() : clearError())}
          status={message ? 'success' : 'error'}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        />
      )}

      <DialogUsers
        open={dialogManager.isOpenDialog}
        handleClose={closeDialog}
        handleUserSelected={handleUserSelected}
        usersSelected={usersSelected}
        role={dialogManager.userRole}
      />

      <Box p={2}>
        <Typography variant="h5">{t('entities.action.detail.title')}</Typography>
      </Box>
      <div className={classes.root}>
        <Grid container spacing={0}>
          <Grid item xs={6}>
            <Box className={classes.boxBackAction} onClick={() => history.push('/')}>
              <ArrowBackIosIcon />
              <Typography variant="h6">{t('entities.action.detail.goBack')}</Typography>
            </Box>
          </Grid>
          <Grid item xs={6}>
            <Box className={`${classes.box} ${classes.boxStatus}`} justifyContent="center">
              <ToggleButtonGroup size="medium" value={view} exclusive onChange={handleChange}>
                <ToggleButton classes={cssView} value="view">
                  {t('entities.detail.view')}
                </ToggleButton>
                <ToggleButton classes={cssView} value="table">
                  {t('entities.detail.table')}
                </ToggleButton>
              </ToggleButtonGroup>
            </Box>
          </Grid>
          <Grid item xs={6}>
            <DetailActionInfo type="action" action={action} />
          </Grid>
          <Grid item xs={6}>
            {view === 'view' ? (
              <Box className={`${classes.paper} ${classes.paperAction}`}>
                <Grid container spacing={1}>
                  <Grid item xs={12}>
                    <Box className={classes.boxButtons}>
                      <Button
                        className={classes.button}
                        startIcon={<ArrowBackIosIcon />}
                        disabled={indexSubAction < 0}
                        onClick={() => handleStepAction(indexSubAction - 1)}
                      />
                      <Typography variant="h5">
                        {t('entities.action.detail.subactionInfo', {
                          from: subActions.length > 0 ? indexSubAction + 1 : 0,
                          to: subActions.length,
                        })}
                      </Typography>
                      <Button
                        className={classes.button}
                        endIcon={<ArrowForwardIosIcon />}
                        disabled={parseInt(indexSubAction + 1, 10) >= subActions.length}
                        onClick={() => handleStepAction(indexSubAction + 1)}
                      />
                    </Box>
                  </Grid>
                </Grid>
              </Box>
            ) : null}

            <Grid container spacing={2}>
              <Grid item xs={12}>
                {view === 'view' ? (
                  <Box p={1}>
                    {actionSelected.subActions.length ? (
                      <DetailActionInfo type="subAction" action={subActions[indexSubAction]} />
                    ) : (
                      <Typography variant="h4">
                        {t('entities.action.detail.noSubaction')}
                      </Typography>
                    )}
                  </Box>
                ) : (
                  <Box p={1}>
                    <DetailActionInfoTable />
                  </Box>
                )}
              </Grid>
              <Grid item xs={12}>
                <Box className={classes.divider} />
              </Grid>
              <Grid item xs={12}>
                <Box paddingLeft={1} paddingRight={1}>
                  <AccordionInfo
                    referenceId={actionSelected?.id}
                    referenceType={allReferenceType.ACTION}
                    referenceTypeNote="ACTION"
                    domain="ACTION"
                  />
                </Box>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </div>
    </Paper>
  ) : null;
};

export default ActionDetailPage;
