import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  Paper,
} from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import React, { useCallback, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Draggable from 'react-draggable';
import { useForm } from 'react-hook-form';
import { Trans, useTranslation } from 'react-i18next';
import { isEmpty } from 'lodash';
import Checkbox from '@material-ui/core/Checkbox';
import LinkIcon from '@material-ui/icons/Link';
import constants from '../../../config/constants';
import InputInfo from '../../Input/InputInfo';
import InputText from '../../Input/InputText';
import InputSelect from '../../Input/InputSelect';
import dialogDhOCStyle from './style/dialogDhOCStyle';
import AttachmentManager from '../../../context/attachment/AttachmentManager';

const PaperComponent = props => (
  <Draggable handle="#draggable-dialog-users" cancel={'[class*="MuiDialogContent-root"]'}>
    {/* eslint-disable-next-line react/jsx-props-no-spreading */}
    <Paper {...props} />
  </Draggable>
);

const DialogDHOC = ({ onClose, open, referenceId, referenceType }) => {
  const maximumAdhocYear = new Date().getFullYear();
  const css = dialogDhOCStyle();
  const { t } = useTranslation();
  const [disabledSearch, setDisabledSearch] = useState(true);
  const {
    searchAdhocAttachment,
    postAdhocAttachment,
    downloadAdhocAttachment,
  } = AttachmentManager();
  const [itemsFound, setItemsFound] = useState(null);
  const [indecesToBeAdded, setIndecesToBeAdded] = useState(new Set());
  const [disabledAdd, setDisabledAdd] = useState(true);

  const { register, handleSubmit, errors, setValue, watch, formState } = useForm({
    mode: 'all',
  });

  const onCloseThisDialog = () => {
    setItemsFound(null);
    setIndecesToBeAdded(new Set());
    setDisabledAdd(true);
    onClose();
  };
  const onClickAdd = async () => {
    const itemsToBeAdded = [];
    Array.from(indecesToBeAdded).forEach(index => itemsToBeAdded.push(itemsFound[index]));
    await postAdhocAttachment(itemsToBeAdded, referenceType, referenceId);
    onCloseThisDialog();
  };

  const onSubmit = async (data, evt) => {
    evt.preventDefault();
    evt.stopPropagation();
    if (formState.isSubmitting) {
      return;
    }
    const newItemsFoundList = await searchAdhocAttachment({
      ...data,
      registry: String(data.registry)?.toUpperCase(),
    });
    setItemsFound(newItemsFoundList);
  };

  const onSelectItem = (index, checked) => {
    const actual = indecesToBeAdded;
    if (checked) {
      actual.add(index);
    } else {
      actual.delete(index);
    }
    setIndecesToBeAdded(actual);
    setDisabledAdd(actual.size === 0);
  };

  useEffect(() => {
    if (!isEmpty(watch())) {
      const check = Object.keys(watch()).filter(key => watch(key) === '');
      setDisabledSearch(check.length !== 0);
    }
  }, [watch]);

  const adhocYearSelectionOptions = useCallback(() => {
    return Array(maximumAdhocYear - constants.ADHOC_MIN_YEAR + 1)
      .fill()
      .map((_, idx) => constants.ADHOC_MIN_YEAR + idx)
      .map(element => {
        return { id: element, value: element };
      });
  }, []);

  const adhocItemFound = (item, index) => {
    return (
      <Box
        className={css.adhocItemsFound}
        textAlign="left"
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="space-between"
        alignItems="center"
        alignContent="flex-start"
        key={index + item.name}
      >
        <Typography
          title={`Scarica ${item.name}`}
          component="div"
          variant="subtitle2"
          noWrap
          style={{ cursor: 'pointer' }}
          onClick={() => downloadAdhocAttachment(item.id, item.name, true)}
        >
          {item.signature}
        </Typography>

        <Typography
          variant="body1"
          color="textSecondary"
          title={`Scarica ${item.name}`}
          style={{ cursor: 'pointer' }}
          noWrap
          onClick={() => downloadAdhocAttachment(item.id, item.name, true)}
        >
          {item.name}
        </Typography>
        <Box className={css.boxAdhocItemsFound}>
          <LinkIcon
            style={{ marginRight: '1rem', cursor: 'pointer' }}
            onClick={() => downloadAdhocAttachment(item.id, item.name, true)}
          />
          <Checkbox onChange={e => onSelectItem(index, e.target.checked)} />
        </Box>
      </Box>
    );
  };

  const searchResults = () => {
    return itemsFound.length === 0 ? (
      <Box className={css.boxAttachment}>
        <Typography variant="h6" component="div" color="textSecondary">
          {t('entities.attachment.dialog.attachment.notFound')}
        </Typography>
      </Box>
    ) : (
      itemsFound.map((item, index) => adhocItemFound(item, index))
    );
  };
  return (
    <Dialog
      fullWidth
      maxWidth="sm"
      onClose={onCloseThisDialog}
      aria-labelledby="dialog-dhoc"
      open={open}
      disableEnforceFocus
      PaperComponent={PaperComponent}
    >
      <DialogTitle className={css.dialogTitle} id="form-dialog-title" onClose={onCloseThisDialog}>
        <div>
          <Trans t={t} i18nKey="entities.attachment.dialog.attachment.titleDhoc">
            {[<span key={0} className={css.highlight} />]}
          </Trans>
        </div>
        <IconButton aria-label="close" className={css.closeButton} onClick={onCloseThisDialog}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <form onSubmit={handleSubmit(onSubmit)}>
          <Box className={css.boxEditor}>
            <Grid container>
              <Grid item xs={12}>
                <InputInfo title={t('entities.attachment.dialog.attachment.protocolDhOc')}>
                  <InputText
                    type="number"
                    name="protocol"
                    placeholder={t('entities.attachment.dialog.attachment.protocolDhOcPlaceholder')}
                    register={register({
                      required: {
                        value: true,
                        message: t('validation.required'),
                      },
                    })}
                    watch={watch}
                    setValue={setValue}
                    errors={errors}
                  />
                </InputInfo>
              </Grid>
              <Grid item xs={12}>
                <InputInfo title={t('entities.attachment.dialog.attachment.yearDhOc')}>
                  <InputSelect
                    register={register({
                      required: {
                        value: true,
                        message: t('validation.required'),
                      },
                    })}
                    name="year"
                    emptyOption={undefined}
                    initialValue={maximumAdhocYear}
                    options={adhocYearSelectionOptions()}
                    errors={errors}
                  />
                </InputInfo>
              </Grid>
              <Grid item xs={12}>
                <InputInfo
                  title={t('entities.attachment.dialog.attachment.registryDhOc')}
                  className={css.uppercase}
                >
                  <InputText
                    name="registry"
                    placeholder={t('entities.attachment.dialog.attachment.registryDhOcPlaceholder')}
                    register={register({
                      required: {
                        value: true,
                        message: t('validation.required'),
                      },
                    })}
                    initialValue={constants.ADHOC_DEFAULT_REGISTRY}
                    watch={watch}
                    setValue={setValue}
                    errors={errors}
                  />
                </InputInfo>
              </Grid>
              <Grid item xs={12}>
                <Box textAlign="center">
                  <Button
                    type="submit"
                    variant="outlined"
                    className={css.buttonSearch}
                    disabled={disabledSearch}
                  >
                    {t('entities.attachment.dialog.attachment.searchDhOC')}
                  </Button>
                </Box>
              </Grid>
            </Grid>
            <Grid item xs={12}>
              <Box textAlign="center">
                {itemsFound === null || itemsFound === undefined ? <></> : searchResults()}
              </Box>
            </Grid>
          </Box>
        </form>
      </DialogContent>
      <DialogActions className={css.dialogActions}>
        <Button disabled={disabledAdd} onClick={onClickAdd} className={css.button}>
          {t('entities.attachment.dialog.attachment.add')}
        </Button>
      </DialogActions>
    </Dialog>
  );
};
DialogDHOC.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  referenceType: PropTypes.string,
  referenceId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

DialogDHOC.defaultProps = {
  referenceType: null,
  referenceId: null,
};
export default DialogDHOC;
