import { Accordion, Box, Typography } from '@material-ui/core';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import AttachmentManager from '../../context/attachment/AttachmentManager';
import AttachmentInfo from '../AttachmentInfo/AttachmentInfo';
import NoteManager from '../../context/note/NoteManager';
import NoteInfo from '../NoteInfo/NoteInfo';
import actionAccordionStyle from './style/accordionInfoStyle';

const AccordionInfo = ({ referenceId, referenceType, referenceTypeNote, domain }) => {
  const [accordion, setAccordion] = useState({ panel1: false, panel2: false });
  const { t } = useTranslation();
  const { attachments } = AttachmentManager();
  const { notes } = NoteManager();

  const handleChangeAccordion = panel => () => {
    setAccordion(state => ({ ...state, [panel]: !state[panel] }));
  };
  const cssAccordion = actionAccordionStyle();

  return (
    <div className={cssAccordion.root}>
      <Accordion
        expanded={accordion.panel1}
        onChange={handleChangeAccordion('panel1')}
        className={cssAccordion.accordion}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
          className={cssAccordion.accordionSummary}
        >
          <Box
            className={cssAccordion.root}
            textAlign="left"
            p={2}
            display="flex"
            flexDirection="row"
            flexWrap="nowrap"
            justifyContent="space-between"
            alignItems="center"
            alignContent="flex-start"
          >
            <Box>
              <Typography variant="body1" color="textPrimary">
                {t('entities.action.attachments')}
              </Typography>
              <Typography variant="body2" color="textSecondary">
                {t('entities.action.attachmentsPresent', { count: attachments.length })}
              </Typography>
            </Box>
            <Box
              p={1}
              onClick={evt => {
                evt.stopPropagation();
                handleChangeAccordion('panel1')();
              }}
            >
              <Typography className={cssAccordion.heading} color="textSecondary">
                {!accordion.panel1 ? t('entities.action.expand') : t('entities.action.close')}
              </Typography>
            </Box>
          </Box>
        </AccordionSummary>
        <AccordionDetails>
          <AttachmentInfo
            referenceId={referenceId}
            referenceType={referenceType}
            attachments={attachments}
            domain={domain}
          />
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={accordion.panel2}
        onChange={handleChangeAccordion('panel2')}
        className={cssAccordion.accordion}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
          className={cssAccordion.accordionSummary}
        >
          <Box
            className={cssAccordion.root}
            textAlign="left"
            p={2}
            display="flex"
            flexDirection="row"
            flexWrap="nowrap"
            justifyContent="space-between"
            alignItems="center"
            alignContent="flex-start"
          >
            <Box>
              <Typography variant="body1" color="textPrimary">
                {t('entities.action.note')}
              </Typography>
              <Typography variant="body2" color="textSecondary">
                {t('entities.action.notePresent', { count: notes.length })}
              </Typography>
            </Box>
            <Box
              p={1}
              onClick={evt => {
                evt.stopPropagation();
                handleChangeAccordion('panel2')();
              }}
            >
              <Typography className={cssAccordion.heading} color="textSecondary">
                {!accordion.panel2 ? t('entities.action.expand') : t('entities.action.close')}
              </Typography>
            </Box>
          </Box>
        </AccordionSummary>
        <AccordionDetails>
          <NoteInfo referenceId={referenceId} referenceType={referenceTypeNote} domain={domain} />
        </AccordionDetails>
      </Accordion>
    </div>
  );
};

AccordionInfo.propTypes = {
  referenceType: PropTypes.string,
  referenceTypeNote: PropTypes.oneOf(['TASK', 'ACTION']).isRequired,
  referenceId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  domain: PropTypes.string,
};

AccordionInfo.defaultProps = {
  referenceType: null,
  referenceId: null,
  domain: null,
};

export default AccordionInfo;
