import { isEmpty } from 'lodash';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { apiTaskAction } from '../../api/taskDetail';
import { getUniqueUsers } from '../../helpers/helper';
import UserManager from '../user/UserManager';
import ActionContext from './ActionContext';

const ActionProvider = ({ children, keycloak, serviceUrl, taskId }) => {
  const [state, setState] = useState({
    taskId,
    actions: [],
    pagination: { number: 0, numberOfElements: 0, size: 5, totalElements: 0, totalePages: 0 },
    error: null,
    message: null,
    loading: false,
    keycloak,
    serviceUrl,
  });
  const { logged } = UserManager();
  const { setUsersPresent } = UserManager();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!isEmpty(logged) && taskId !== null) {
          setState(prevState => ({ ...prevState, loading: true }));
          let actions = await apiTaskAction(serviceUrl, taskId);
          const { payload } = actions;
          const usersActions = getUniqueUsers(payload);
          setUsersPresent(usersActions);
          actions = payload.map((action, index) => ({ ...action, index }));
          setState(prevState => ({ ...prevState, actions, selected: 0 }));
        }
      } catch (err) {
        console.error('errore', err);
        setState(prevState => ({ ...prevState, actions: [], error: err.toString() }));
      } finally {
        setState(prevState => ({ ...prevState, loading: false }));
      }
    };
    const authenticated = keycloak.initialized && keycloak.authenticated;
    if (authenticated) {
      fetchData();
    }
  }, [keycloak.authenticated, keycloak.initialized, logged, taskId]);

  return <ActionContext.Provider value={[state, setState]}> {children} </ActionContext.Provider>;
};

ActionProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  keycloak: PropTypes.shape({ initialized: PropTypes.bool, authenticated: PropTypes.bool }),
  serviceUrl: PropTypes.string.isRequired,
  taskId: PropTypes.string.isRequired,
};

ActionProvider.defaultProps = {
  keycloak: {
    initialized: false,
    authenticated: false,
  },
};

export default ActionProvider;
