import PropTypes from 'prop-types';
import React from 'react';
import { Dialog, Divider, List, ListItem, ListItemText } from '@material-ui/core';
import { Trans, useTranslation } from 'react-i18next';
import dialogAttachmentStyle from './style/dialogAttachmentStyle';

const DialogAttachment = ({ onClose, open }) => {
  const handleListItemClick = value => {
    onClose(value);
  };

  const { t } = useTranslation();
  const css = dialogAttachmentStyle();

  return (
    <Dialog onClose={onClose} aria-labelledby="dialog-choose-attachment" open={open}>
      <List>
        <ListItem button onClick={() => handleListItemClick('dhoc')} key="dhoc">
          <ListItemText>
            <Trans t={t} i18nKey="entities.attachment.dhoc">
              {[<span key={0} className={css.highlight} />]}
            </Trans>
          </ListItemText>
        </ListItem>
        <Divider />
        <ListItem button onClick={() => handleListItemClick('elcloud')} key="elcloud">
          <ListItemText>
            <Trans t={t} i18nKey="entities.attachment.elcloud">
              {[<span key={0} className={css.highlight} />]}
            </Trans>
          </ListItemText>
        </ListItem>
      </List>
    </Dialog>
  );
};

DialogAttachment.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};

export default DialogAttachment;
