import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { apiTaskDetail } from '../../api/taskDetail';
import { ROLES } from '../../helpers/helper';
import loadingManager from '../loading/LoadingManager';
import UserManager from '../user/UserManager';
import TaskContext from './TaskContext';

const TaskProvider = ({ children, keycloak, serviceUrl, taskId }) => {
  const [state, setState] = useState({
    task: {
      id: taskId,
    },
    metadata: {},
    message: null,
    error: null,
    serviceUrl,
    edoResponsible: null,
    usersSelected: {},
  });
  const { setUsersPresent } = UserManager();

  const { setLoading } = loadingManager();
  const { t } = useTranslation();

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const json = await apiTaskDetail(serviceUrl, taskId);
        const { payload, metadata } = json;
        const usersTask = [
          ...new Set(
            Array.from(ROLES).reduce((acc, key) => {
              if (payload[key]) {
                acc.push(payload[key]);
              }
              return acc;
            }, [])
          ),
        ];
        setUsersPresent(usersTask);
        setState(prev => {
          return {
            ...prev,
            task: payload,
            metadata,
            edoResponsible: payload.responsible,
            usersSelected: ROLES.reduce((acc, role) => {
              acc[role] = payload[role];
              return acc;
            }, {}),
            error: null,
          };
        });
      } catch (err) {
        setState(prev => {
          return { ...prev, error: t('error.dataLoading') };
        });
      } finally {
        setLoading(false);
      }
    };
    const authenticated = keycloak.initialized && keycloak.authenticated;
    if (authenticated && taskId !== null) {
      fetchData();
    }
  }, [keycloak.authenticated, keycloak.initialized, taskId]);

  return <TaskContext.Provider value={[state, setState]}> {children} </TaskContext.Provider>;
};

TaskProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  keycloak: PropTypes.shape({ initialized: PropTypes.bool, authenticated: PropTypes.bool }),
  serviceUrl: PropTypes.string.isRequired,
  taskId: PropTypes.string.isRequired,
};

TaskProvider.defaultProps = {
  keycloak: {
    initialized: false,
    authenticated: false,
  },
};

export default TaskProvider;
