export const getKeycloakToken = () => {
  if (
    window &&
    window.entando &&
    window.entando.keycloak &&
    window.entando.keycloak.authenticated
  ) {
    return window.entando.keycloak.token;
  }
  return '';
};

export const getDefaultOptions = () => ({
  headers: new Headers({
    Authorization: `Bearer ${getKeycloakToken()}`,
    'Content-Type': 'application/json',
  }),
});

export const request = async (url, options, haveBodyResponse = true) => {
  const response = await fetch(url, options);
  if (response.status === 204) {
    return {};
  }
  if (response.status >= 200 && response.status < 300) {
    if (haveBodyResponse) {
      return response.json();
    }
    return {};
  }
  return Promise.reject(new Error(response.statusText || response.status));
};

export const requestBlobOld = async (url, options) => {
  const response = await fetch(url, options);
  if (response.status === 200) {
    const filename = response.headers.get('Content-Disposition').split('filename=')[1];
    response.blob().then(blob => {
      const otherUrl = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = otherUrl;
      a.download = filename;
      a.click();
    });
    return {
      filename,
    };
  }
  return Promise.reject(new Error(response.statusText || response.status));
};

export const requestBlob = async (url, options, previewPdf = false) => {
  const response = await fetch(url, options);
  if (response.status === 200) {
    const filename = response.headers.get('Content-Disposition').split('filename=')[1];
    response.blob().then(blob => {
      const otherUrl = window.URL.createObjectURL(blob);

      if (previewPdf && filename.toLowerCase().endsWith('.pdf')) {
        const pdfWindow = window.open();
        pdfWindow.location.href = otherUrl;
      } else {
        const a = document.createElement('a');
        a.href = otherUrl;
        a.download = filename;
        a.click();
      }
    });
    return null;
  }
  return Promise.reject(new Error(response.statusText || response.status));
};
