import { useContext } from 'react';
import DialogContext from './DialogContext';

const DialogManager = () => {
  const [state, setState] = useContext(DialogContext);

  const setType = type => {
    setState(prev => ({ ...prev, type }));
  };

  const setOpenDialog = openDialog => {
    setState(prev => ({ ...prev, openDialog }));
  };

  const setUserSelected = userSelected => setState(prev => ({ ...prev, userSelected }));

  const setUserRole = userRole => setState(prev => ({ ...prev, userRole }));

  const setAllState = newState => setState(newState);

  return {
    isOpenDialog: state.openDialog,
    userSelected: state.userSelected,
    userRole: state.userRole,
    type: state.type,
    setAllState,
    setType,
    setUserRole,
    setOpenDialog,
    setUserSelected,
  };
};
export default DialogManager;
