import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { apiUserByFiscalCodeGet, apiUsersGet } from '../../api/users';

import UserContext from './UserContext';

const UserManager = () => {
  const [state, setState] = useContext(UserContext);
  const { serviceUrl, usersPresent, logged } = state;

  const { t } = useTranslation();

  const setUsers = users => setState(prev => ({ ...prev, users }));

  const setUsersPresent = users => {
    setState(prev => ({ ...prev, usersPresent: [...new Set(prev.usersPresent.concat(users))] }));
  };

  const setLoadUserPresent = load => setState(prev => ({ ...prev, loadUsersPresent: load }));

  const fetchUserPresent = async edoResponsible => {
    const promises = usersPresent.map(user =>
      apiUserByFiscalCodeGet(serviceUrl, user.toUpperCase(), user === edoResponsible)
    );
    Promise.all(promises).then(json => {
      const fetchUsers = json.reduce((acc, data) => {
        const user = data.payload;
        const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
        acc[user.fiscalCode.toUpperCase()] = {
          ...user,
          role,
        };
        return acc;
      }, {});
      setUsers(fetchUsers);
    });
  };

  const fetchUser = async (pagination, search, informedOrConsulted) => {
    try {
      const json = await apiUsersGet(serviceUrl, pagination, search, informedOrConsulted);
      const {
        payload,
        totalPages,
        totalElements,
        numberOfElements,
        number,
        size,
        first,
        last,
      } = json;
      if (payload) {
        const users = payload.reduce((acc, user) => {
          const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
          acc[user.fiscalCode.toUpperCase()] = {
            ...user,
            role,
          };
          return acc;
        }, {});
        const userLogged = state.users[logged];
        const userLoggedPresent = Object.keys(users).find(
          key => users[key]?.fiscalCode.toUpperCase() === logged
        );

        setState(prev => ({
          ...prev,
          users: userLoggedPresent
            ? users
            : { ...users, [userLogged?.fiscalCode.toUpperCase()]: userLogged },
          pagination: {
            totalPages,
            totalElements,
            numberOfElements,
            number,
            size,
            first,
            last,
          },
        }));
      }
    } catch (e) {
      setState({ ...state, error: t('error.dataLoading'), message: null });
    }
  };
  return {
    logged: state.logged,
    vertexId: state.vertexId,
    vertexShortName: state.vertexShortName,
    users: state.users,
    usersPresent: state.usersPresent,
    loadUsersPresent: state.loadUsersPresent,
    serviceUrl: state.serviceUrl,
    setUsers,
    setUsersPresent,
    setLoadUserPresent,
    fetchUser,
    fetchUserPresent,
  };
};

export default UserManager;
