import { createMuiTheme } from '@material-ui/core';
import { itIT } from '@material-ui/core/locale';

import { jssPreset, StylesProvider, ThemeProvider } from '@material-ui/core/styles';

import KeycloakContext from 'auth/KeycloakContext';
import { INPUT_EVENT_TYPES, KEYCLOAK_EVENT_TYPE } from 'custom-elements/widgetEventTypes';
import { subscribeToWidgetEvent } from 'helpers/widgetEvents';
import setLocale from 'i18n/setLocale';
import { create as jssCreate } from 'jss';
import React from 'react';
import ReactDOM from 'react-dom';
import retargetEvents from 'react-shadow-dom-retarget-events';
import { AuthenticatedView } from '../auth/KeycloakViews';
import AppPage from '../components/Pages/AppPage';
import ActionProvider from '../context/action/ActionProvider';
import DialogProvider from '../context/dialog/DialogProvider';
import LoadingProvider from '../context/loading/LoadingProvider';
import NoteProvider from '../context/note/NoteProvider';
import TaskProvider from '../context/task/TaskProvider';
import UserProvider from '../context/user/UserProvider';
import AttachmentProvider from '../context/attachment/AttachmentProvider';

const getKeycloakInstance = () =>
  (window &&
    window.entando &&
    window.entando.keycloak && { ...window.entando.keycloak, initialized: true }) || {
    initialized: false,
  };

const ATTRIBUTES = {
  hidden: 'hidden',
  locale: 'locale',
  disableDefaultEventHandler: 'disable-default-event-handler', // custom element attribute names MUST be written in kebab-case
  serviceUrl: 'service-url',
  serviceUrlUser: 'service-url-user',
  serviceUrlAttachments: 'service-url-attachments',
  taskId: 'task-id',
  cssUrl: 'css-url',
};

// Mock Service Worker https://mswjs.io/    --removed use-mock
if (process.env.NODE_ENV === 'development') {
  // eslint-disable-next-line global-require
  const { worker } = require('../components/__mocks__/browser');
  worker.start();
}

class TaskDetailElement extends HTMLElement {
  jss;

  container;

  mountPoint;

  keycloak = getKeycloakInstance();

  unsubscribeFromTaskEvent;

  unsubscribeFromKeycloakEvent;

  defaultEventHandlerDisabled;

  muiTheme;

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }

  attributeChangedCallback(name, oldValue, newValue) {
    if (!this.container || oldValue === newValue) {
      return;
    }
    if (!Object.values(ATTRIBUTES).includes(name)) {
      throw new Error(`Untracked changed attribute: ${name}`);
    }
    if (name === ATTRIBUTES.disableDefaultEventHandler) {
      this.onToggleDisableDefaultEvent();
    }
    this.render();
  }

  connectedCallback() {
    this.container = document.createElement('div');
    this.mountPoint = document.createElement('div');
    this.container.appendChild(this.mountPoint);

    const cssUrl = this.getAttribute(ATTRIBUTES.cssUrl);

    const linkElem = document.createElement('link');
    linkElem.setAttribute('rel', 'stylesheet');
    linkElem.setAttribute('href', `${cssUrl}/suneditor.min.css`);

    const shadowRoot = this.attachShadow({ mode: 'open' });
    shadowRoot.appendChild(linkElem);
    shadowRoot.appendChild(this.container);

    this.jss = jssCreate({
      ...jssPreset(),
      insertionPoint: this.container,
    });

    this.muiTheme = createMuiTheme(
      {
        props: {
          MuiDialog: {
            container: this.mountPoint,
          },
          MuiPopover: {
            container: this.mountPoint,
          },
        },
      },
      itIT
    );

    this.keycloak = { ...getKeycloakInstance(), initialized: true };

    this.unsubscribeFromKeycloakEvent = subscribeToWidgetEvent(KEYCLOAK_EVENT_TYPE, () => {
      this.keycloak = { ...getKeycloakInstance(), initialized: true };
      this.render();
    });

    this.unsubscribeFromTaskEvent = subscribeToWidgetEvent(INPUT_EVENT_TYPES.detailTask, evt => {
      console.log('detail Task', evt);
    });

    this.onToggleDisableDefaultEvent();

    this.render();

    retargetEvents(shadowRoot);
  }

  disconnectedCallback() {
    if (this.unsubscribeFromKeycloakEvent) {
      this.unsubscribeFromKeycloakEvent();
    }
    if (this.unsubscribeFromTaskEvent) {
      this.unsubscribeFromTaskEvent();
    }
  }

  onToggleDisableDefaultEvent() {
    const disableEventHandler = this.getAttribute(ATTRIBUTES.disableDefaultEventHandler) === 'true';

    if (disableEventHandler !== this.defaultEventHandlerDisabled) {
      // eslint-disable-next-line no-empty
      if (!disableEventHandler) {
      } else {
        if (this.unsubscribeFromWidgetEvents) {
          this.unsubscribeFromWidgetEvents();
        }
        if (this.unsubscribeFromKeycloakEvent) {
          this.unsubscribeFromKeycloakEvent();
        }
      }
      this.defaultEventHandlerDisabled = disableEventHandler;
    }
  }

  render() {
    const serviceUrl = this.getAttribute(ATTRIBUTES.serviceUrl) || '';
    const serviceUrlUser = this.getAttribute(ATTRIBUTES.serviceUrlUser) || '';
    const serviceUrlAttachments = this.getAttribute(ATTRIBUTES.serviceUrlAttachments) || '';
    const hidden = this.getAttribute(ATTRIBUTES.hidden) === 'true';
    const taskId = this.getAttribute(ATTRIBUTES.taskId);

    if (hidden) {
      ReactDOM.render(<></>);
      return;
    }
    const locale = this.getAttribute(ATTRIBUTES.locale) || 'it';
    setLocale(locale);

    ReactDOM.render(
      <KeycloakContext.Provider value={this.keycloak}>
        <LoadingProvider>
          <UserProvider keycloak={this.keycloak} serviceUrl={serviceUrlUser}>
            <TaskProvider keycloak={this.keycloak} serviceUrl={serviceUrl} taskId={taskId}>
              <ActionProvider keycloak={this.keycloak} serviceUrl={serviceUrl} taskId={taskId}>
                <NoteProvider keycloak={this.keycloak} serviceUrl={serviceUrl} taskId={taskId}>
                  <AttachmentProvider
                    keycloak={this.keycloak}
                    serviceUrlAttachments={serviceUrlAttachments}
                    taskId={taskId}
                  >
                    <DialogProvider>
                      <StylesProvider jss={this.jss}>
                        <ThemeProvider theme={this.muiTheme}>
                          <AuthenticatedView keycloak={this.keycloak}>
                            <AppPage />
                          </AuthenticatedView>
                        </ThemeProvider>
                      </StylesProvider>
                    </DialogProvider>
                  </AttachmentProvider>
                </NoteProvider>
              </ActionProvider>
            </TaskProvider>
          </UserProvider>
        </LoadingProvider>
      </KeycloakContext.Provider>,
      this.mountPoint
    );
  }
}

customElements.define('task-detail', TaskDetailElement);
