import { Box, Grid } from '@material-ui/core';
import { format, parseISO } from 'date-fns';
import { it } from 'date-fns/locale';
import { get } from 'lodash';
import React from 'react';
import { Trans, useTranslation } from 'react-i18next';
import DialogManager from '../../context/dialog/DialogManager';
import TaskManager from '../../context/task/TaskManager';
import UserManager from '../../context/user/UserManager';
import { ROLES } from '../../helpers/helper';
import { OPTIONS_TASK_STATE } from '../__types__/types';
import BoxInfo from '../BoxInfo/BoxInfo';
import BoxInfoWithAvatar from '../BoxInfo/BoxInfoWithAvatar';
import InputInfoWithPopper from '../Input/InputInfoWithPopper';
import InputSelect from '../Input/InputSelect';
import taskInfo from './style/taskInfoStyle';

const TaskInfo = () => {
  const { task, updateTaskStatus } = TaskManager();
  const { setAllState } = DialogManager();
  const { users } = UserManager();
  const { t } = useTranslation();
  const css = taskInfo();

  const { status, canComplete, id } = task;

  const openDialog = (userRole, userSelected) => {
    setAllState({
      type: 'task',
      openDialog: true,
      userSelected,
      userRole,
    });
  };

  const showBoxWithAvatar = () => {
    if (users) {
      return Array.from(ROLES).map(m => (
        <Grid item xs={12} key={m}>
          <BoxInfoWithAvatar
            title={t(`entities.info.${m}`)}
            name={users[task[m]] ? `${users[task[m]]?.firstname} ${users[task[m]]?.lastname}` : ''}
            role={get(users[`${task[m]}`], 'role', t('entities.info.empty'))}
            avatar={`${task[m]}.jpg`}
            cta={() => openDialog(m, users[task[m]])}
          />
        </Grid>
      ));
    }
    return null;
  };

  const handleChangeStatus = newStatus => {
    if (status !== newStatus) {
      updateTaskStatus(id, newStatus);
    }
  };

  return (
    <>
      <Grid container spacing={2} className={css.gridContainer}>
        <Grid item xs={6}>
          <BoxInfo title={t('entities.info.id')} content={task.id} />
        </Grid>
        <Grid item xs={6}>
          <Box className={css.boxState}>
            <InputInfoWithPopper
              title={t('entities.action.state')}
              contentPopper={{
                title: (
                  <Trans t={t} i18nKey="entities.action.popper.title">
                    {[<span key={0} className={css.highlight} />]}
                  </Trans>
                ),
                description: t('entities.action.popper.description'),
              }}
            >
              <InputSelect
                name="status"
                options={OPTIONS_TASK_STATE}
                initialValue={status || ''}
                emptyOption={t('entities.action.noStatus')}
                disabled={canComplete === false}
                onChange={handleChangeStatus}
              />
            </InputInfoWithPopper>
          </Box>
        </Grid>

        <Grid item xs={12}>
          <BoxInfo title={t('entities.info.task')} content={task.title} />
        </Grid>
        <Grid item xs={12}>
          <Grid container spacing={3}>
            <Grid item xs={6}>
              <BoxInfo
                title={t('entities.info.startDate')}
                content={
                  task.startDate
                    ? format(parseISO(task.startDate), 'dd MMMM yyyy', { locale: it })
                    : ''
                }
                textTransform="capitalize"
              />
            </Grid>
            <Grid item xs={6}>
              <BoxInfo
                title={t('entities.info.endDate')}
                content={
                  task.endDate ? format(parseISO(task.endDate), 'dd MMMM yyyy', { locale: it }) : ''
                }
                textTransform="capitalize"
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <BoxInfo title={t('entities.info.description')} content={task.description} />
        </Grid>
        {showBoxWithAvatar()}
      </Grid>
    </>
  );
};

export default TaskInfo;
