import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControlLabel,
  Grid,
  IconButton,
  InputBase,
  Paper,
  Radio,
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import CloseIcon from '@material-ui/icons/Close';
import SearchIcon from '@material-ui/icons/Search';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import Draggable from 'react-draggable';
import { useTranslation } from 'react-i18next';
import UserManager from '../../../context/user/UserManager';
import ListUser from './ListUser';
import dialogUserStyle from './style/dialogUserStyle';

const PaperComponent = props => (
  <Draggable handle="#draggable-dialog-users" cancel={'[class*="MuiDialogContent-root"]'}>
    {/* eslint-disable-next-line react/jsx-props-no-spreading */}
    <Paper {...props} />
  </Draggable>
);

const DialogUsers = ({ open, handleClose, handleUserSelected, role, usersSelected }) => {
  const { t } = useTranslation();
  const [selectedValue, setSelectedValue] = useState(null);
  const [usersSelectedObj, setUsersSelectedObj] = useState({});
  const [search, setSearch] = useState('');
  const theme = useTheme();
  const { logged, users, fetchUser } = UserManager();

  // eslint-disable-next-line no-unused-vars
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const css = dialogUserStyle();

  const usePreviousValues = values => {
    const ref = useRef();
    useEffect(() => {
      ref.current = values;
    });
    return ref.current;
  };
  const preSearch = usePreviousValues(search);

  const handleClick = event => {
    const { value } = event.target;

    setSelectedValue(value.toUpperCase());
  };

  const onClickAssignRole = () => {
    if (handleUserSelected) {
      if (selectedValue) {
        handleUserSelected(users[selectedValue], role);
      }
    }
    setSelectedValue(null);
    setSearch('');
    handleClose();
  };

  const onClickClose = () => {
    setSelectedValue(null);
    setSearch('');
    handleClose();
  };

  const handleSearch = searchValue => {
    fetchUser(null, searchValue, role === 'consulted' || role === 'informed');
  };

  useEffect(() => {
    if (selectedValue) {
      const obj = { ...usersSelectedObj, [role]: users[selectedValue] };
      setUsersSelectedObj(obj);
    }
  }, [selectedValue]);

  useEffect(() => {
    const userObj = Object.keys(usersSelected).reduce((acc, key) => {
      acc[key] = users[usersSelected[key]];
      return acc;
    }, {});
    setUsersSelectedObj(userObj);
  }, [usersSelected, users]);

  const checkDisabled = () => {
    if (Object.keys(usersSelected).length > 0) {
      return !!Object.keys(usersSelectedObj)
        .map(key => usersSelectedObj[key]?.fiscalCode.toUpperCase())
        .find(f => f === logged.toUpperCase());
    }
    return false;
  };

  return (
    <Dialog
      data-testid="dialog-user"
      fullWidth
      maxWidth="sm"
      open={open}
      onClose={() => onClickClose()}
      PaperComponent={PaperComponent}
      disableEnforceFocus
      aria-labelledby="form-dialog-title"
      className={css.dialog}
    >
      <DialogTitle className={css.dialogTitle} id="form-dialog-title" onClose={onClickClose}>
        <div>
          {t('entities.dialog.users.title')} <span className={css.role}>{role}</span>
        </div>
        <IconButton aria-label="close" className={css.closeButton} onClick={onClickClose}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Box className={css.boxTools}>
              <Box className={css.boxInput}>
                <InputBase
                  id="search"
                  className={css.input}
                  name="search"
                  value={search}
                  placeholder={t('entities.dialog.users.search')}
                  inputProps={{ 'aria-label': t('entities.dialog.users.search') }}
                  onKeyUp={event => {
                    const {
                      key,
                      target: { value },
                    } = event;
                    setSearch('');
                    setSearch(value);
                    if (key === 'Enter') {
                      handleSearch(value);
                    }
                  }}
                />
                <IconButton
                  className={`${css.iconButton} ${search !== preSearch ? 'active' : ''}`}
                  aria-label="search"
                  disabled={search === ''}
                  onClick={() => handleSearch(search)}
                >
                  <SearchIcon />
                </IconButton>
              </Box>
              <Box className={css.boxAssign}>
                <FormControlLabel
                  value="start"
                  disabled={checkDisabled()}
                  style={{
                    pointerEvents: `${checkDisabled() ? 'none' : 'auto'}`,
                  }}
                  control={
                    // eslint-disable-next-line react/jsx-wrap-multilines
                    <Radio
                      className={css.radio}
                      disabled={checkDisabled()}
                      checked={
                        usersSelectedObj &&
                        usersSelectedObj[role]?.fiscalCode.toUpperCase() === logged
                      }
                    />
                  }
                  label={t('entities.dialog.users.assign')}
                  labelPlacement="start"
                  onClick={() => handleClick({ target: { value: logged } })}
                />
              </Box>
            </Box>
            <Box>
              {Object.keys(users ?? {}).length > 0 ? (
                <ListUser
                  selectedValue={selectedValue}
                  handleClick={handleClick}
                  search={search}
                  usersSelected={usersSelectedObj}
                  role={role}
                />
              ) : null}
            </Box>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions className={css.dialogActions}>
        <Button
          onClick={onClickClose}
          className={css.buttonClose}
          data-testid="dialog-user-close-button"
        >
          {t('entities.dialog.users.buttonClose')}
        </Button>
        <Button
          onClick={onClickAssignRole}
          className={css.button}
          disabled={selectedValue === null}
          data-testid="dialog-user-assign-button"
        >
          {t('entities.dialog.users.button')}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

DialogUsers.propTypes = {
  open: PropTypes.bool,
  handleClose: PropTypes.func.isRequired,
  usersSelected: PropTypes.shape({
    fiscalCode: PropTypes.string,
  }),
  handleUserSelected: PropTypes.func.isRequired,
  role: PropTypes.string,
};

DialogUsers.defaultProps = {
  open: false,
  role: '',
  usersSelected: undefined,
};

export default DialogUsers;
