import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { apiNotesGet } from '../../api/taskDetail';
import UserManager from '../user/UserManager';
import NoteContext from './NoteContext';

const NoteProvider = ({ children, keycloak, serviceUrl, taskId }) => {
  const [state, setState] = useState({
    taskId,
    notes: [],
    error: null,
    message: null,
    keycloak,
    serviceUrl,
  });
  const { setUsersPresent, setLoadUserPresent } = UserManager();

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (taskId !== null) {
          let notes = await apiNotesGet(serviceUrl, taskId);
          notes = notes.payload;
          const usersNote = [...new Set(notes.map(n => n.createdBy))];
          setLoadUserPresent(false);
          setUsersPresent(usersNote);
          setLoadUserPresent(true);
          // setState(prevState => ({ ...prevState, taskId }));
        }
      } catch (err) {
        setState(prevState => ({ ...prevState, notes: [], error: err.toString() }));
      }
    };
    const authenticated = keycloak.initialized && keycloak.authenticated;
    if (authenticated) {
      fetchData();
    }
  }, [keycloak.authenticated, keycloak.initialized, taskId]);

  return <NoteContext.Provider value={[state, setState]}>{children} </NoteContext.Provider>;
};

NoteProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  taskId: PropTypes.string.isRequired,
  keycloak: PropTypes.shape({ authenticated: PropTypes.bool, initialized: PropTypes.bool }),
  serviceUrl: PropTypes.string.isRequired,
};

NoteProvider.defaultProps = {
  keycloak: {
    initialized: false,
    authenticated: false,
  },
};
export default NoteProvider;
