import { Box, Button, IconButton, Typography } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import { format } from 'date-fns';
import { it } from 'date-fns/locale';
import React, { useEffect, useState } from 'react';

import { useTranslation } from 'react-i18next';

import PropTypes from 'prop-types';
import NoteManager from '../../context/note/NoteManager';
import TaskManager from '../../context/task/TaskManager';
import UserManager from '../../context/user/UserManager';
import image from '../__mocks__/user.jpg';

import BoxInfoNote from '../BoxInfo/BoxInfoNote';
import Notification from '../common/Notification';
import DialogNote from '../Dialog/Note/DialogNote';
import noteInfoStyle from './style/noteInfoStyle';

const NoteInfo = ({ referenceId, referenceType, domain }) => {
  const { users } = UserManager();
  const {
    notes,
    fetchNotes,
    deleteNote,
    postNote,
    message,
    error,
    clearMessage,
    clearError,
  } = NoteManager();
  const { metadata } = TaskManager();
  const css = noteInfoStyle();
  const { t } = useTranslation();
  const [openDialog, setOpenDialog] = useState(false);

  const handleDialogNote = () => {
    setOpenDialog(prev => !prev);
  };

  const addNote = note => {
    const payload = {
      id: notes.length + 1,
      description: note,
    };
    postNote(referenceId, referenceType, payload);
  };

  useEffect(() => {
    if (referenceId && referenceType) {
      fetchNotes(referenceId, referenceType);
    }
  }, [referenceId, referenceType]);

  return users ? (
    <>
      {message === null && error === null ? null : (
        <Notification
          message={message || error}
          onClose={() => (message ? clearMessage() : clearError())}
          status={message ? 'success' : 'error'}
          anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        />
      )}
      <Box className={css.boxContainer}>
        <DialogNote open={openDialog} handleClose={handleDialogNote} handleAddNote={addNote} />

        <Box textAlign="center">
          <Button
            size="large"
            variant="contained"
            className={css.button}
            startIcon={<AddIcon />}
            disableRipple
            onClick={handleDialogNote}
          >
            <Typography variant="body2" color="textSecondary">
              {domain === 'ACTION'
                ? t('entities.attachment.addNoteAction')
                : t('entities.attachment.addNote')}
            </Typography>
          </Button>
        </Box>

        {notes
          .sort((a, b) => (a.id > b.id ? -1 : 1))
          .map((note, index) => (
            <Box key={note.id} textAlign="left" marginTop={2}>
              <Box p={1} display="flex" alignItems="center" justifyContent="space-between">
                <Typography variant="body2" color="textSecondary">
                  {t('entities.attachment.note', { from: index + 1, to: notes.length })}
                </Typography>
                {metadata.canRemoveNotes ? (
                  <IconButton
                    aria-label="delete"
                    onClick={() => deleteNote(referenceId, referenceType, note.id)}
                    disableRipple
                  >
                    <DeleteIcon />
                  </IconButton>
                ) : null}
              </Box>
              <BoxInfoNote
                content={note.description}
                dateNote={format(
                  new Date(note?.createdDate ?? new Date()),
                  'dd MMMM yyyy: kk:mm:ss',
                  {
                    locale: it,
                  }
                )}
                name={
                  users[note.createdBy]
                    ? `${users[note.createdBy]?.firstname} ${users[note.createdBy]?.lastname}`
                    : ''
                }
                avatar={image}
              />
            </Box>
          ))}
      </Box>
    </>
  ) : null;
};

NoteInfo.propTypes = {
  referenceType: PropTypes.string,
  referenceId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  domain: PropTypes.string,
};
NoteInfo.defaultProps = {
  referenceType: null,
  referenceId: null,
  domain: null,
};

export default NoteInfo;
