import { Avatar, Box, IconButton, Typography } from '@material-ui/core';
import CreateIcon from '@material-ui/icons/Create';
import PropTypes from 'prop-types';
import React from 'react';
import TaskManager from '../../context/task/TaskManager';
import boxInfoWithAvatar from './style/boxInfoWithAvatarStyle';

const BoxInfoWithAvatar = ({ avatar, title, name, role, cta }) => {
  const { metadata } = TaskManager();

  const css = boxInfoWithAvatar();
  return (
    <>
      <Box textAlign="left">
        <Typography variant="body1" color="textSecondary">
          {title}
        </Typography>
      </Box>
      <Box
        className={css.borderBox}
        textAlign="left"
        p={2}
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="flex-start"
        alignItems="center"
        alignContent="flex-start"
      >
        <Box display="flex" flexWrap="nowrap" alignItems="center" width={1}>
          <Box p={1} marginRight={2}>
            <Avatar alt={name} src={avatar} />
          </Box>
          <Box>
            <Typography variant="body1" color="textPrimary">
              {name}
            </Typography>
            <Typography variant="body2" color="textSecondary">
              {role}
            </Typography>
          </Box>
        </Box>
        {metadata.canChangeTaskRoles ? (
          <Box>
            <IconButton data-testid={title} aria-label="change" onClick={cta} disableRipple>
              <CreateIcon />
            </IconButton>
          </Box>
        ) : null}
      </Box>
    </>
  );
};

BoxInfoWithAvatar.propTypes = {
  title: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  role: PropTypes.string.isRequired,
  avatar: PropTypes.string.isRequired,
  cta: PropTypes.func,
};

BoxInfoWithAvatar.defaultProps = {
  cta: () => {},
};

export default BoxInfoWithAvatar;
