import {
  Avatar,
  List,
  ListItem,
  ListItemAvatar,
  ListItemSecondaryAction,
  ListItemText,
  ListSubheader,
  Radio,
} from '@material-ui/core';
import { uniqueId } from 'lodash';
import PropTypes from 'prop-types';
import React, { useEffect, useRef, useState } from 'react';
import useLoadMoreOnScroll from 'react-hook-pagination';
import { useTranslation } from 'react-i18next';
import { apiUsersGet } from '../../../api/users';
import UserManager from '../../../context/user/UserManager';
import { userMock } from '../../__mocks__/mocks';
import dialogUserStyle from './style/dialogUserStyle';

const getStickyArray = array => {
  const seen = new Set();
  return array
    .filter(m => m.id !== 'undefined')
    .filter(f => !f.hidden)
    .filter(el => {
      const surname = el.lastname.charAt(0).toUpperCase();
      const duplicate = seen.has(surname);
      seen.add(surname);
      return !duplicate;
    })
    .map(m => m.lastname.charAt(0).toUpperCase());
};

const ListUser = ({ handleClick, search, usersSelected, role }) => {
  const { logged, users, useMock, serviceUrl, setUsers, setError } = UserManager();

  const [stickySurname, setStickySurname] = useState([]);
  const [usersData, setUsersData] = useState([]);

  const scroller = useRef();
  const { t } = useTranslation();
  const css = dialogUserStyle();

  const { start, end, setIsFetching, forceDonefetching } = useLoadMoreOnScroll({
    fetchSize: 40,
    scroller,
    limit: 100000,
  });
  // eslint-disable-next-line consistent-return,no-shadow
  const fetchUserLocal = async (start, end) => {
    try {
      const pageSize = end - start;
      const page = end / pageSize - 1;
      let json;
      if (useMock) {
        json = await new Promise(resolve => setTimeout(() => resolve(userMock), 100));
      } else {
        json = await apiUsersGet(
          serviceUrl,
          { page, pageSize },
          search,
          role === 'consulted' || role === 'informed'
        );
      }
      const { payload, totalPages, number } = json;
      if (totalPages < number) {
        forceDonefetching();
      }
      return payload.reduce((acc, user) => {
        // eslint-disable-next-line no-shadow
        const role = `${user.degree} ${user.edo ? '-' : ''} ${user?.edo?.name ?? ''}`;
        acc[user.fiscalCode.toUpperCase()] = {
          ...user,
          role,
          hidden: user.fiscalCode.toUpperCase() === logged,
        };
        return acc;
      }, {});
    } catch (e) {
      setError(t('error.dataLoading'));
    }
  };

  useEffect(() => {
    return () => {
      setUsers({ [logged]: users[logged] });
    };
  }, []);

  useEffect(() => {
    if (start !== end) {
      setIsFetching(true);
      fetchUserLocal(start, end).then(usr => {
        setUsers({ ...users, ...usr });
        setIsFetching(false);
      });
    }
  }, [start, end]);

  useEffect(() => {
    const array = Object.keys(users).reduce((acc, key) => {
      if (key !== 'undefined') {
        acc.push({ id: key, ...users[key] });
      }
      return acc;
    }, []);
    setStickySurname(getStickyArray(array));
    setUsersData(array);
  }, [users]);

  return (
    <List className={css.list} subheader={<li />} ref={scroller}>
      {stickySurname.map((section, index) => (
        <li key={`section-${section}`} className={css.listSection}>
          <ul className={`${css.ul} ${index === 0 ? 'first' : ''}`}>
            <ListSubheader classes={{ root: css.subHeader }}>{section}</ListSubheader>
            {usersData
              .filter(f => !f.hidden)
              .filter(f => f.lastname.charAt(0).toUpperCase() === section)
              .map(u => (
                <ListItem key={uniqueId('list-item')} disabled={u.selected}>
                  <ListItemAvatar>
                    <Avatar />
                  </ListItemAvatar>
                  <ListItemText primary={`${u.firstname} ${u.lastname}`} secondary={u.role} />
                  <ListItemSecondaryAction>
                    <Radio
                      className={css.radio}
                      checked={
                        usersSelected &&
                        usersSelected[role]?.fiscalCode.toUpperCase() === u.fiscalCode.toUpperCase()
                      }
                      onClick={handleClick}
                      value={u.fiscalCode.toUpperCase()}
                      name={`radio-button-${u.id}`}
                      inputProps={{ 'aria-label': u.id }}
                      disabled={
                        !!Object.keys(usersSelected)
                          .filter(f => usersSelected[f])
                          .map(key => usersSelected[key].fiscalCode.toUpperCase())
                          .find(f => f === u.fiscalCode.toUpperCase())
                      }
                    />
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
          </ul>
        </li>
      ))}
    </List>
  );
};

ListUser.propTypes = {
  handleClick: PropTypes.func.isRequired,
  search: PropTypes.string.isRequired,
  usersSelected: PropTypes.shape({}).isRequired,
  role: PropTypes.string,
};

ListUser.defaultProps = {
  role: '',
};

export default ListUser;
