import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  IconButton,
  Paper,
} from '@material-ui/core';
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import CloseIcon from '@material-ui/icons/Close';
import PropTypes from 'prop-types';
import React, { useRef, useState } from 'react';
import Draggable from 'react-draggable';
import { useTranslation } from 'react-i18next';
import SunEditor from 'suneditor-react';

import dialogUserStyle from './style/dialogNoteStyle';

const PaperComponent = props => (
  <Draggable handle="#draggable-dialog-users" cancel={'[class*="MuiDialogContent-root"]'}>
    {/* eslint-disable-next-line react/jsx-props-no-spreading */}
    <Paper {...props} />
  </Draggable>
);

const OPTIONS = {
  mode: 'classic',
  defaultTag: 'div',
  height: 'auto',
  minHeight: '100px',
  maxHeight: '250px',
  showPathLabel: false,
  toolbarContainer: '#custom_toolbar',
  rtl: false,
  charCounter: true,
  charCounterType: 'char',
  maxCharCount: '500',
  imageResizing: false,
  imageHeightShow: false,
  imageFileInput: false,
  imageUrlInput: false,
  videoResizing: false,
  videoHeightShow: false,
  videoFileInput: false,
  videoUrlInput: false,
  videoRatioShow: false,
  audioUrlInput: false,
  tabDisable: false,
  shortcutsHint: false,
  buttonList: [['bold', 'underline', 'italic', 'align', 'link']],
};

const DialogNote = ({ open, handleClose, handleAddNote }) => {
  const { t } = useTranslation();
  const theme = useTheme();
  // eslint-disable-next-line no-unused-vars
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const css = dialogUserStyle();
  const editorRef = useRef(null);
  const [note, setNote] = useState('');

  const handleChange = content => {
    // eslint-disable-next-line no-control-regex
    setNote(content.replace(/[\u200B\u0000-\u001F\u007F-\u009F]/g, ''));
  };

  const onClickPublish = () => {
    if (handleAddNote) {
      const newNote = note.replaceAll('<p>', '<div>').replaceAll('</p>', '</div>');
      // eslint-disable-next-line no-control-regex
      handleAddNote(newNote.replace(/[\u200B\u0000-\u001F\u007F-\u009F]/g, ''));
    }
    handleClose();
  };

  return (
    <Dialog
      fullWidth
      maxWidth="md"
      open={open}
      onClose={() => handleClose()}
      PaperComponent={PaperComponent}
      disableEnforceFocus
      aria-labelledby="form-dialog-title"
      className={css.dialog}
    >
      <DialogTitle className={css.dialogTitle} id="form-dialog-title" onClose={() => handleClose()}>
        <div>{t('entities.dialog.note.title')}</div>
        <IconButton aria-label="close" className={css.closeButton} onClick={() => handleClose()}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={css.dialogContent}>
        <Box className={css.boxEditor}>
          <SunEditor
            height={400}
            name="nota"
            ref={editorRef}
            placeholder={t('entities.dialog.note.placeholder')}
            lang="it"
            autoFocus
            setOptions={OPTIONS}
            onChange={handleChange}
          />
        </Box>
      </DialogContent>
      <DialogActions className={css.dialogActions}>
        <Button onClick={onClickPublish} className={css.button}>
          {t('entities.dialog.note.buttonPublish')}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

DialogNote.propTypes = {
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired,
  handleAddNote: PropTypes.func.isRequired,
};

export default DialogNote;
