import { Box, Button, Grid, Paper, Typography } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import { isEmpty } from 'lodash';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import ActionManager from '../../context/action/ActionManager';
import AttachmentManager from '../../context/attachment/AttachmentManager';
import DialogManager from '../../context/dialog/DialogManager';
import TaskManager from '../../context/task/TaskManager';
import UserManager from '../../context/user/UserManager';
import ViewManager from '../../context/view/ViewManager';
import AccordionInfo from '../Action/AccordionInfo';
import ActionInfo from '../Action/ActionInfo';
import ActionInfoTable from '../Action/ActionInfoTable';
import Notification from '../common/Notification';
import DialogUsers from '../Dialog/Users/DialogUsers';
import Status from '../Status/Status';
import TaskInfo from '../Task/TaskInfo';
import { taskDetailPageStyle, taskDetailPageViewStyle } from './style/taskDetailPageStyle';

const TaskDetailPage = () => {
  const { t } = useTranslation();
  // eslint-disable-next-line no-unused-vars
  const [indexAction, setIndexAction] = useState(0);
  const [action, setAction] = useState({});
  const classes = taskDetailPageStyle();
  const cssView = taskDetailPageViewStyle();
  const { users } = UserManager();
  const taskManager = TaskManager();
  const { task, updateTask, usersSelected } = taskManager;
  const actionManager = ActionManager();
  const { actions, setSelectAction } = actionManager;
  const history = useHistory();
  const { view, setView } = ViewManager();
  const { isOpenDialog, setOpenDialog, userRole, type } = DialogManager();
  const { allReferenceType } = AttachmentManager();

  const handleStepAction = index => {
    if (index >= 0 && index <= actions.length) {
      setIndexAction(index);
      setAction(actions[index]);
      setSelectAction(index);
    }
  };

  const handleChange = (event, value) => {
    setView(value);
  };
  useEffect(() => {
    if (!isEmpty(actions)) {
      setAction(actions[indexAction]);
    }
  }, [actions]);

  const closeDialog = () => {
    setOpenDialog(false);
  };

  const handleUserSelected = (user, role) => {
    if (type === 'task') {
      updateTask({ ...task, [role]: user.fiscalCode.toUpperCase() });
    }
  };

  let message;
  let clearMessage;
  let error;
  let clearError;

  if (taskManager.message !== null) {
    message = taskManager.message;
    clearMessage = taskManager.clearMessage;
  } else if (actionManager.message !== null) {
    message = actionManager.message;
    clearMessage = actionManager.clearMessage;
  }

  if (taskManager.error !== null) {
    message = taskManager.error;
    clearError = taskManager.clearError;
  } else if (actionManager.error !== null) {
    error = actionManager.error;
    clearError = actionManager.clearError;
  }

  const navigationBarAction = (
    <Box border={0} className={`${classes.box} ${classes.boxAction}`}>
      <Box
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="space-between"
        alignItems="center"
      >
        <Box className={classes.boxButtons}>
          <Button
            className={classes.button}
            startIcon={<ArrowBackIosIcon />}
            disabled={indexAction < 0}
            onClick={() => handleStepAction(indexAction - 1)}
          />
          <Typography variant="h5">
            {t('entities.detail.action', {
              from: actions.length > 0 ? indexAction + 1 : 0,
              to: actions.length,
            })}
          </Typography>
          <Button
            className={classes.button}
            endIcon={<ArrowForwardIosIcon />}
            disabled={parseInt(indexAction + 1, 10) >= actions.length}
            onClick={() => handleStepAction(indexAction + 1)}
          />
        </Box>
        <Button
          variant="contained"
          size="large"
          className={classes.buttonDetail}
          onClick={() => history.push('/dettaglio-azione')}
          endIcon={<ArrowForwardIosIcon />}
        >
          {t('entities.action.gotoDetail')}
        </Button>
      </Box>
    </Box>
  );

  return (
    <>
      <Notification
        message={message}
        onClose={clearMessage}
        status="success"
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
      />
      <Notification
        message={error}
        onClose={clearError}
        status="error"
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
      />
      <Paper elevation={5}>
        {users && isOpenDialog ? (
          <DialogUsers
            open={isOpenDialog}
            handleClose={closeDialog}
            handleUserSelected={handleUserSelected}
            usersSelected={usersSelected}
            role={userRole}
          />
        ) : null}

        <Box p={2}>
          <Typography variant="h5">{t('entities.detail.title', { id: task.id })}</Typography>
        </Box>
        <div className={classes.root}>
          <Grid container>
            <Grid item xs={6}>
              <Box className={`${classes.box} ${classes.boxStatus}`}>
                <Status priority={task.priority ?? '0'} />
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box className={`${classes.box} ${classes.boxStatus}`} justifyContent="center">
                <ToggleButtonGroup size="medium" value={view} exclusive onChange={handleChange}>
                  <ToggleButton classes={cssView} value="view">
                    {t('entities.detail.view')}
                  </ToggleButton>
                  <ToggleButton classes={cssView} value="table">
                    {t('entities.detail.table')}
                  </ToggleButton>
                </ToggleButtonGroup>
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Container fixed disableGutters className={classes.containerInfo}>
                <Container fixed disableGutters className={classes.containerInfoScroll}>
                  <Box p={1}>
                    <TaskInfo />
                  </Box>
                </Container>
              </Container>
            </Grid>
            <Grid item xs={6}>
              {view === 'view' && actions.length ? navigationBarAction : null}
              {view === 'view' ? (
                <Container fixed disableGutters className={classes.containerInfoView}>
                  <Container fixed disableGutters className={classes.containerInfoScrollView}>
                    <Box p={1}>
                      {actions.length ? (
                        <ActionInfo action={action} />
                      ) : (
                        <Typography variant="h4">{t('entities.action.detail.noAction')}</Typography>
                      )}
                    </Box>
                  </Container>
                </Container>
              ) : (
                <Grid container spacing={2}>
                  <Grid item xs={12} style={{ maxHeight: '50vh' }}>
                    <Container fixed disableGutters className={classes.containerInfoTable}>
                      <Container fixed disableGutters className={classes.containerInfoScrollTable}>
                        <Box p={1}>
                          <ActionInfoTable />
                        </Box>
                      </Container>
                    </Container>
                  </Grid>
                  <Grid item xs={12}>
                    <Box className={classes.divider} />
                  </Grid>
                  <Grid item xs={12} style={{ maxHeight: '50vh' }}>
                    <Container fixed disableGutters className={classes.containerInfoTable}>
                      <Container fixed disableGutters className={classes.containerInfoScrollTable}>
                        <Box p={1}>
                          <AccordionInfo
                            referenceId={task.id}
                            referenceType={allReferenceType.TASK}
                            referenceTypeNote="TASK"
                          />
                        </Box>
                      </Container>
                    </Container>
                  </Grid>
                </Grid>
              )}
            </Grid>
          </Grid>
        </div>
      </Paper>
    </>
  );
};
export default TaskDetailPage;
