import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { ROLES } from '../../helpers/helper';
import UserManager from '../user/UserManager';
import TaskContext from './TaskContext';
import loadingManager from '../loading/LoadingManager';
import {
  apiTaskDetail,
  apiTaskUpdatePut,
  apiUpdateCompletionPercentage,
  apiUpdateStatusTask,
} from '../../api/taskDetail';

const TaskManager = () => {
  const [state, setState] = useContext(TaskContext);

  const clearError = () => setState(prev => ({ ...prev, error: null }));

  const clearMessage = () => setState(prev => ({ ...prev, message: null }));

  const { setLoading } = loadingManager();
  const { setUsersPresent } = UserManager();
  const { t } = useTranslation();
  const { serviceUrl } = state;

  const fetchTask = async taskId => {
    try {
      setLoading(true);
      const json = await apiTaskDetail(serviceUrl, taskId);
      const { payload } = json;
      setState(prev => {
        return {
          ...prev,
          task: payload,
        };
      });
    } catch (e) {
      console.error('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  const updateTaskCompletionPercentage = async (taskId, completionPercentage) => {
    try {
      setLoading(true);
      const requestBody = { value: completionPercentage };
      const response = await apiUpdateCompletionPercentage(serviceUrl, taskId, requestBody);
      if (response.payload.id) {
        setState(prev => ({
          ...prev,
          completionPercentage,
          message: t('message.status'),
        }));
      }
    } catch (e) {
      console.log('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  const updateTask = async newTask => {
    try {
      setLoading(true);
      const result = await apiTaskUpdatePut(serviceUrl, newTask);
      console.log('updateTask', result);
      if (result.payload) {
        const json = await apiTaskDetail(serviceUrl, newTask.id);
        const { payload, metadata } = json;
        const usersTask = [
          ...new Set(
            Array.from(ROLES).reduce((acc, key) => {
              if (payload[key]) {
                acc.push(payload[key]);
              }
              return acc;
            }, [])
          ),
        ];
        setUsersPresent(usersTask);
        setState(prev => {
          return {
            ...prev,
            task: payload,
            metadata,
            edoResponsible: payload.responsible,
            usersSelected: ROLES.reduce((acc, role) => {
              acc[role] = payload[role];
              return acc;
            }, {}),
            error: null,
          };
        });
      }
    } catch (e) {
      console.log('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  const updateTaskStatus = async (taskId, status) => {
    try {
      setLoading(true);
      const response = await apiUpdateStatusTask(serviceUrl, taskId, { status });
      if (response.payload.id) {
        fetchTask(taskId);
        setState(prev => {
          return {
            ...prev,
            message: t('message.status'),
          };
        });
      }
    } catch (e) {
      console.log('errore', e);
      setState(prev => ({ ...prev, error: t('error.dataLoading') }));
    } finally {
      setLoading(false);
    }
  };

  return {
    usersTask: state.usersTask,
    task: state.task,
    metadata: state.metadata,
    edoResponsible: state.edoResponsible,
    error: state.error,
    message: state.message,
    usersSelected: state.usersSelected,
    clearError,
    clearMessage,
    updateTaskCompletionPercentage,
    updateTask,
    updateTaskStatus,
    fetchTask,
  };
};

export default TaskManager;
