import { Box, Grid } from '@material-ui/core';
import { format, parseISO } from 'date-fns';
import { it } from 'date-fns/locale';
import PropTypes from 'prop-types';
import React from 'react';
import { Trans, useTranslation } from 'react-i18next';
import ActionManager from '../../context/action/ActionManager';
import AttachmentManager from '../../context/attachment/AttachmentManager';
import { OPTIONS_STATE } from '../__types__/types';
import BoxInfo from '../BoxInfo/BoxInfo';
import InputInfoWithPopper from '../Input/InputInfoWithPopper';
import InputSelect from '../Input/InputSelect';
import AccordionInfo from './AccordionInfo';
import { actionInfo } from './style/actionInfoStyle';

const ActionInfo = ({ action }) => {
  const css = actionInfo();
  const { t } = useTranslation();
  const { taskId, updateActionStatus } = ActionManager();
  const { allReferenceType } = AttachmentManager();

  const handleChangeStatus = status => {
    // eslint-disable-next-line react/prop-types
    const { id } = action;
    updateActionStatus(id, status);
  };

  // eslint-disable-next-line react/prop-types
  const { title, endDate, subActions, canComplete, status } = action;
  // eslint-disable-next-line react/prop-types
  const countSubActions = subActions?.length || 0;

  const titleSubactions = (
    <Trans t={t} i18nKey="entities.action.subActions" count={countSubActions}>
      {[
        <span key={countSubActions} className={css.subActions}>
          {{ countSubActions }}
        </span>,
      ]}
    </Trans>
  );

  return (
    <>
      <Grid container spacing={2} className={css.gridContainer}>
        <Grid item xs={12}>
          <BoxInfo title={t('entities.action.action')} content={title} />
        </Grid>
        <Grid item xs={12}>
          <Grid container spacing={3}>
            <Grid item xs={6}>
              <Box>
                <BoxInfo
                  title={t('entities.action.within')}
                  content={endDate && format(parseISO(endDate), 'dd MMMM yyyy', { locale: it })}
                  textTransform="capitalize"
                />
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box className={css.boxState}>
                <InputInfoWithPopper
                  title={t('entities.action.state')}
                  contentPopper={{
                    title: (
                      <Trans t={t} i18nKey="entities.action.popper.title">
                        {[<span key={0} className={css.highlight} />]}
                      </Trans>
                    ),
                    description: t('entities.action.popper.description'),
                  }}
                >
                  <InputSelect
                    name="status"
                    options={OPTIONS_STATE}
                    initialValue={status ?? ''}
                    emptyOption={t('entities.action.noStatus')}
                    highlightKey={OPTIONS_STATE[0].id}
                    disabledKey={canComplete === false ? OPTIONS_STATE[1].id : null}
                    onChange={handleChangeStatus}
                    // disabled={!metadata?.canChangeCompletionStatus}
                  />
                </InputInfoWithPopper>
              </Box>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Box className={css.boxInfoSubAction}>
            <InputInfoWithPopper
              title={titleSubactions}
              contentPopper={{
                title: '',
                description: t('entities.action.popper.subAction'),
              }}
              icon="left"
            />
          </Box>
        </Grid>

        <Grid item xs={12}>
          <AccordionInfo
            referenceId={taskId}
            referenceType={allReferenceType.TASK}
            referenceTypeNote="TASK"
          />
        </Grid>
      </Grid>
    </>
  );
};
ActionInfo.propTypes = {
  action: PropTypes.shape({}).isRequired,
};
export default ActionInfo;
