import Interweave from 'interweave';
import boxInfoNoteStyle from './style/boxInfoNoteStyle';

const BoxInfoNote = ({ content, name, dateNote, avatar }) => {
  const css = boxInfoNoteStyle();
  return (
    <Box>
      <Box className={css.borderBox} textAlign="left" p={2}>
        <Typography variant="body2" color="textPrimary" component="div">
          <Interweave content={content} />
        </Typography>
      </Box>
      <Box
        textAlign="left"
        p={2}
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="flex-start"
        alignItems="center"
        alignContent="flex-start"
      >
        <Box p={1} marginRight={2}>
          <Avatar alt="" src={avatar} />
        </Box>
        <Box>
          <Typography variant="body2" color="textSecondary">
            {dateNote}
          </Typography>
          <Typography variant="body1" color="textPrimary">
            {name}
          </Typography>
        </Box>
      </Box>
    </Box>
  );
};
BoxInfoNote.propTypes = {
  content: PropTypes.string.isRequired,
  dateNote: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  avatar: PropTypes.string.isRequired,
};

export default BoxInfoNote;
