import { getDefaultOptions, request } from 'api/helpers';

const resourceByFiscalCode = 'users/fiscalCode';

export const apiUserByFiscalCodeGet = async (serviceUrl, userId, loadEdoTree) => {
  let url = `${serviceUrl}/${resourceByFiscalCode}/${userId}`;
  if (loadEdoTree) {
    url += `?loadEdoTree=true`;
  }
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiUsersGet = async (
  serviceUrl,
  pagination = {
    page: 0,
    pageSize: 40,
  },
  search,
  informedOrConsulted = false
) => {
  let url = `${serviceUrl}/users?informedOrConsulted=${informedOrConsulted}&sortBy=lastname,firstname,id`;
  if (pagination) {
    url += `&page=${pagination.page}&size=${pagination.pageSize}`;
  }
  if (search) {
    url += `&globalSearch=${search}`;
  }
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};
