import { Box, Paper, Typography } from '@material-ui/core';
import Popper from '@material-ui/core/Popper';
import InfoIcon from '@material-ui/icons/Info';
import PropTypes from 'prop-types';
import React, { useRef, useState } from 'react';
import popperStyle from './style/inputInfoWithPopperStyle';

const InputInfoWithPopper = ({ title, children, contentPopper, icon, ...boxProps }) => {
  const [open, setOpen] = React.useState(false);
  const classes = popperStyle();
  const anchorRef = useRef(null);
  const [arrowRef, setArrowRef] = useState(null);

  const handleClick = () => {
    setOpen(prev => !prev);
  };

  const Icon = (
    <Box onClick={handleClick} ref={anchorRef} height="20px" marginLeft={1} marginRight={1}>
      <InfoIcon fontSize="small" />
    </Box>
  );

  return (
    // eslint-disable-next-line react/jsx-props-no-spreading
    <Box {...boxProps}>
      <Popper
        open={open}
        anchorEl={anchorRef.current}
        className={classes.popper}
        placement="top"
        disablePortal
        modifiers={{
          flip: {
            enabled: true,
          },
          arrow: {
            enabled: true,
            element: arrowRef,
          },
          preventOverflow: {
            enabled: 'true',
            boundariesElement: 'scrollParent',
          },
        }}
      >
        <span className={classes.arrow} ref={setArrowRef} />
        <Paper className={classes.paper}>
          <Typography variant="body1">{contentPopper.title}</Typography>
          <Typography variant="body2"> {contentPopper.description}</Typography>
        </Paper>
      </Popper>
      <Box
        border={0}
        textAlign="left"
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        alignItems="center"
        justifyContent="flex-start"
      >
        {icon === 'left' ? Icon : null}

        <Box marginRight={2}>
          <Typography variant="body1" color="textSecondary">
            {title}
          </Typography>
        </Box>
        {icon === 'right' ? Icon : null}
      </Box>
      <Box border={0} textAlign="left">
        {children}
      </Box>
    </Box>
  );
};

InputInfoWithPopper.propTypes = {
  title: PropTypes.oneOfType([PropTypes.element, PropTypes.string]).isRequired,
  children: PropTypes.node,
  icon: PropTypes.oneOf(['left', 'right']),
  contentPopper: PropTypes.shape({
    title: PropTypes.oneOfType([PropTypes.element, PropTypes.string]).isRequired,
    description: PropTypes.string.isRequired,
  }).isRequired,
};

InputInfoWithPopper.defaultProps = {
  children: null,
  icon: 'right',
};

export default InputInfoWithPopper;
