import { Box, FormControl, OutlinedInput, Typography } from '@material-ui/core';
import FormHelperText from '@material-ui/core/FormHelperText';
import { isEmpty } from 'lodash';
import PropTypes from 'prop-types';
import React, { useEffect } from 'react';

const InputText = ({
  register,
  errors,
  name,
  placeholder,
  watch,
  debug,
  showCountCaracthers,
  countCaracthers,
  multiline,
  rows,
  rowsMax,
  initialValue,
  setValue,
  cssClass,
  type,
}) => {
  if (debug) {
    console.log('watch', watch(name));
  }

  useEffect(() => {
    if (initialValue && setValue) {
      setValue(name, initialValue);
    }
  }, [initialValue, setValue, name]);

  return (
    <FormControl variant="outlined" fullWidth error={!isEmpty(errors[name])}>
      <Box position="relative" width={1}>
        <OutlinedInput
          type={type}
          fullWidth
          id={`component-${name}`}
          placeholder={placeholder}
          inputRef={register}
          name={name}
          multiline={multiline}
          rows={rows}
          rowsMax={rowsMax}
          className={cssClass}
        />
        {showCountCaracthers ? (
          <Box position="absolute" bottom={0} right="10px">
            <Typography variant="caption" color="textSecondary">
              {watch(name) ? watch(name).length : 0} / {countCaracthers}
            </Typography>
          </Box>
        ) : null}
      </Box>
      <FormHelperText id={`component-${name}-error-text`}>{errors[name]?.message}</FormHelperText>
    </FormControl>
  );
};

InputText.propTypes = {
  register: PropTypes.func.isRequired,
  watch: PropTypes.func.isRequired,
  setValue: PropTypes.func.isRequired,
  name: PropTypes.string.isRequired,
  placeholder: PropTypes.string.isRequired,
  errors: PropTypes.shape({}).isRequired,
  debug: PropTypes.bool,
  multiline: PropTypes.bool,
  showCountCaracthers: PropTypes.bool,
  countCaracthers: PropTypes.number,
  rows: PropTypes.number,
  rowsMax: PropTypes.number,
  initialValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  cssClass: PropTypes.string,
  type: PropTypes.string,
};

InputText.defaultProps = {
  debug: false,
  multiline: false,
  showCountCaracthers: false,
  countCaracthers: 0,
  rows: 2,
  rowsMax: 4,
  initialValue: '',
  cssClass: '',
  type: 'text',
};

export default InputText;
