import {
  Button,
  Dialog,
  DialogActions,
  DialogTitle,
  Grid,
  IconButton,
  Paper,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { DropzoneAreaBase } from 'material-ui-dropzone';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import Draggable from 'react-draggable';
import { Trans, useTranslation } from 'react-i18next';
import { useForm } from 'react-hook-form';
import DialogContent from '@material-ui/core/DialogContent';
import { isEmpty } from 'lodash';
import AttachmentManager from '../../../context/attachment/AttachmentManager';
import dialogElCloudStyle from './style/dialogElCloudStyle';
import InputInfo from '../../Input/InputInfo';
import InputText from '../../Input/InputText';

const PaperComponent = props => (
  <Draggable handle="#draggable-dialog-users" cancel={'[class*="MuiDialogContent-root"]'}>
    {/* eslint-disable-next-line react/jsx-props-no-spreading */}
    <Paper {...props} />
  </Draggable>
);

const DialogElCloud = ({ onClose, open, referenceId, referenceType }) => {
  const css = dialogElCloudStyle();
  const { t } = useTranslation();
  const { attachments, postNextcloudAttachment } = AttachmentManager();
  const [disabledAdd, setDisabledAdd] = useState(true);

  const { register, errors, setValue, watch } = useForm({
    mode: 'all',
  });

  useEffect(() => {
    if (!isEmpty(watch())) {
      const check = Object.keys(watch()).filter(key => watch(key) === '');
      if (check.length === 0) {
        setDisabledAdd(false);
      } else {
        setDisabledAdd(true);
      }
    }
  }, [watch]);

  const onSubmit = async () => {
    await postNextcloudAttachment(Object.values(watch())[0], referenceType, referenceId);
    onClose();
  };

  return (
    <Dialog
      fullWidth
      maxWidth="sm"
      onClose={onClose}
      aria-labelledby="dialog-dhoc"
      open={open}
      disableEnforceFocus
      PaperComponent={PaperComponent}
    >
      <DialogTitle className={css.dialogTitle} id="form-dialog-title" onClose={onClose}>
        <div>
          <Trans t={t} i18nKey="entities.attachment.dialog.attachment.titleElCloud">
            {[<span key={0} className={css.highlight} />]}
          </Trans>
        </div>
        <IconButton aria-label="close" className={css.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent>
        <DropzoneAreaBase
          dropzoneClass={css.dropZoneArea}
          dialogProps={{
            'aria-labelledby': 'dialog-elcloud',
            disableEnforceFocus: true,
            PaperComponent,
            className: css.dropZoneArea,
          }}
          dropzoneProps={{ disabled: true }}
          maxWidth="sm"
          fileObjects={attachments}
          cancelButtonText=""
          submitButtonText=""
          dropzoneText={t('entities.attachment.dialog.attachment.dragAndDrop')}
          maxFileSize={5000000}
          filesLimit={1}
          open={open}
          onAdd={attachment => {
            // postNextcloudAttachment(attachment);
            onClose(attachment);
          }}
          onClose={onClose}
          showPreviews={false}
          showFileNamesInPreview={false}
          showAlerts={false}
          showFileNames={false}
          showPreviewsInDropzone={false}
        />
        <Grid item xs={12}>
          <InputInfo title={t('entities.attachment.dialog.attachment.linkNextcloud')}>
            <InputText
              name="nextcloudLink"
              placeholder={t('entities.attachment.dialog.attachment.linkNextcloudPlaceholder')}
              register={register({
                required: {
                  value: true,
                  message: t('validation.required'),
                },
              })}
              watch={watch}
              setValue={setValue}
              errors={errors}
            />
          </InputInfo>
        </Grid>
      </DialogContent>
      <DialogActions className={css.dialogActions}>
        <Button className={css.button} disabled={disabledAdd} onClick={onSubmit}>
          {t('entities.attachment.dialog.attachment.add')}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

DialogElCloud.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  referenceType: PropTypes.string,
  referenceId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
};

DialogElCloud.defaultProps = {
  referenceType: null,
  referenceId: null,
};
export default DialogElCloud;
